<template>
  <div>
    <div
      @click="open = !open"
      class="bg-white flex rounded-t justify-between items-center p-6 cursor-pointer card-shadows relative z-10"
    >
      <div class="flex items-center space-x-4">
        <img class="h-8" src="/static/img/icons-primary/user.svg" />
        <h1 class="text-primary-3 text-lg font-semibold">Personal Details</h1>
      </div>
      <div class="flex items-center space-x-4">
        <router-link :to="`/workspace/resume/content/personal`">
          <button
            class="bg-white rounded border py-2 border-edit flex items-center justify-center px-4"
          >
            <img src="/static/img/icon/heroicons/pencil-alt.svg" class="w-6 h-6 mr-2" />
            Edit
          </button>
        </router-link>

        <img
          v-imgSrc="`icon/ico-chevron-up.svg`"
          :class="{ 'rotate-180': open }"
          class="transform"
        />
      </div>
    </div>
    <div v-if="open" class="bg-white p-6 rounded-b">
      <div class="flex items-center">
        <div class="px-8">
          <img :class="avatarClass" :src="avatarUrl" />
        </div>
        <div class="ml-6">
          <h1 class="font-semibold text-primary-1">
            {{ personal.firstname }} {{ personal.lastname }}
          </h1>
          <table class="mt-5">
            <tr class="">
              <th class="py-2 text-left text-primary-3 font-normal pr-6">
                Email:
              </th>
              <td class="text-primary-4">{{ personal.email }}</td>
            </tr>
            <tr class="">
              <th class="py-2 text-left text-primary-3 font-normal pr-6">
                Phone:
              </th>
              <td class="text-primary-4">{{ personal.phone }}</td>
            </tr>
            <tr class="">
              <th
                class="py-2 text-left text-primary-3 font-normal align-top pr-6"
              >
                Address:
              </th>
              <td class="py-2 text-primary-4">
                {{ personal.street }} {{ personal.street ? ',' : '' }}
                <br />
                {{ personal.state }} {{ personal.state ? ',' : '' }}
                {{ personal.city }} {{ personal.city ? ',' : '' }}
                {{ personal.country }}
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { empty } from '@/utils'

export default {
  name: 'PreviewPersonal',
  data() {
    return {
      open: true,
      defaultAvatar: '/static/img/icon/heroicons/camera-e2e8f0.svg',
    }
  },
  props: ['personal'],
  computed: {
    avatarClass: function () {
      return {
        'w-24': true,
        'h-24': true,
        'rounded-full': this.avatarStencilClass,
        'overflow-hidden': true,
        'p-1': this.avatarUrl == this.defaultAvatar ? true : false,
      }
    },
    avatarUrl: function () {
      if (!empty(this.$store.state.rcontent.content.personal)) {
        if (this.$store.state.rcontent.content.personal.avatar) {
          return this.$store.state.rcontent.content.personal.avatar
        }
      }

      return this.defaultAvatar
    },
    avatarStencilClass: function () {
      if (!empty(this.$store.state.rcontent.content.personal)) {
        if (this.$store.state.rcontent.content.personal.stencil == 'circle-stencil') {
          return true
        }

        return false
      }

      return true
    }
  },
  mounted() {
    if (this.$store.state.main.dashboardName) {
      let personal = JSON.parse(JSON.stringify(this.$store.state.rcontent.content.personal));
      let name = this.$store.state.main.dashboardName.split(' ')
      personal.firstname = name[0]
      if (name.length > 1) {
        personal.lastname = name[1]
      }
      this.$store.commit('rcontent/setPersonal', personal)
      this.$store.commit('main/updateDashboardName', '')
    }
  }
}
</script>
