const mutations = {
  setDesignByKey(state, payload) {
    eval(`state.${payload.key} = payload.value `)
  },
  setDefaultSetting(state) {
    const sky = this.state.main.previewtemplate
    switch (sky) {
      case 'contentLetter1':
        state.design = JSON.parse(JSON.stringify(state.contentLetter1))
        break
      case 'contentLetter2':
        state.design = JSON.parse(JSON.stringify(state.contentLetter2))
        break
      case 'contentLetter3':
        state.design = JSON.parse(JSON.stringify(state.contentLetter3))
        break
      case 'contentLetter4':
        state.design = JSON.parse(JSON.stringify(state.contentLetter4))
        break
      case 'contentLetter5':
        state.design = JSON.parse(JSON.stringify(state.contentLetter5))
        break
      case 'contentLetter6':
        state.design = JSON.parse(JSON.stringify(state.contentLetter6))
        break
      default:
        state.design = JSON.parse(JSON.stringify(state.contentLetter1))
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations
}
