<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p 
        v-if="!index"
        :style="sectionTitleStyle"
      >
        {{ content.titleAlias }}
      </p>
      <div style="display: flex; padding-top: 12px;">
        <div style="width: 25%">
          <p style="color: #3E7A69; font-size: 16px;"> 
            {{ data.startDate }}
            {{ data.startDate && data.endDate ? '-' : '' }}
            {{ data.endDate }}
          </p>
        </div>
        <div style="width: 55%">
          <p style="font-weight: 600;color: #000;font-size: 16px;margin-bottom: 6px;">
            {{ data.name }}
            {{ data.name && data.position ? ', ' : '' }}
            <span style="font-weight: 400;">
            {{ data.position }}
            </span>
          </p>
          <div class="ql-editor p-0" v-html="data.description"></div>
        </div>
        <div style="width: 20%; text-align: right;"> 
          <p style="font-size: 16px;color: #333;">
            {{ data.city }}
            {{ data.city && data.country ? ',' : '' }}
            {{ data.country }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle'],
  updated() {
    this.$emit('onUpdate')
  }
}
</script>