import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/views/Dashboard.vue'
import ResumeContent from '@/components/views/ResumeContent.vue'
// import ResumeContentRouter from '@/components/views/ResumeContentRouter.vue'
import Design from '@/components/views/Design.vue'
import Check from '@/components/views/Check.vue'
import Profile from '@/components/views/Profile.vue'
import Setting from '@/components/views/Setting.vue'
import Home from '@/components/views/Home.vue'
import Website from '@/components/views/Website.vue'
import NewContentPersonal from '@/components/views/ResumeContent/NewContentPersonal.vue'
import NewContentEducation from '@/components/views/ResumeContent/NewContentEducation.vue'
import NewProfesionalExperience from '@/components/views/ResumeContent/NewProfesionalExperience.vue'
import NewSkill from '@/components/views/ResumeContent/NewSkill.vue'
import NewProfile from '@/components/views/ResumeContent/NewProfile.vue'
import NewLanguage from '@/components/views/ResumeContent/NewLanguage.vue'
import NewCertificate from '@/components/views/ResumeContent/NewCertificate.vue'
import NewInterest from '@/components/views/ResumeContent/NewInterest.vue'
import NewProject from '@/components/views/ResumeContent/NewProject.vue'
import NewCourses from '@/components/views/ResumeContent/NewCourses.vue'
import NewAwards from '@/components/views/ResumeContent/NewAwards.vue'
import NewReferences from '@/components/views/ResumeContent/NewReferences.vue'
import NewOrganizations from '@/components/views/ResumeContent/NewOrganizations.vue'
import NewPublication from '@/components/views/ResumeContent/NewPublication.vue'
import NewDeclaration from '@/components/views/ResumeContent/NewDeclaration.vue'
import NewCustom from '@/components/views/ResumeContent/NewCustom.vue'
import CoverLetterContent from '@/components/views/CoverLetterContent.vue'
import TemplatesResume from '@/components/views/TemplatesResume.vue'
import TemplatesCoverletter from '@/components/views/TemplatesCoverletter.vue'
import WorkSpaceSection from '@/components/views/WorkSpace/WorkSpaceSection.vue'
import Finalize from '@/components/views/Finalize.vue'
Vue.use(Router)


export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'welcome',
      component: Home
    },
    {
      path: '/templates/resume',
      name: 'resumeTemplates',
      component: TemplatesResume
    },
    {
      path: '/templates/coverletter',
      name: 'coverletterTemplates',
      component: TemplatesCoverletter
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/workspace/coverletter/content',
      component: WorkSpaceSection,
      children: [
        {
          path: '',
          component: CoverLetterContent,
          name: 'coverletterContent',
        }
      ]
    },
    {
      path: '/workspace/resume/content',
      component: WorkSpaceSection,
      children: [
        {
          path: '',
          component: ResumeContent,
          name: 'resumeContent',
        },
        {
          path: 'personal',
          component: NewContentPersonal,
          name: 'rpersonal',
        },
        {
          path: 'education/:id?',
          component: NewContentEducation,
          name: 'reducation',
        },
        {
          path: 'experience/:id?',
          component: NewProfesionalExperience,
          name: 'rexperience',
        },
        {
          path: 'skills/:id?',
          component: NewSkill,
          name: 'rskills',
        },
        {
          path: 'profile/:id?',
          component: NewProfile,
          name: 'rprofile',
        },
        {
          path: 'languages/:id?',
          component: NewLanguage,
          name: 'rlanguages',
        },
        {
          path: 'certificates/:id?',
          component: NewCertificate,
          name: 'rcertificates',
        },
        {
          path: 'interests/:id?',
          component: NewInterest,
          name: 'rinterests',
        },
        {
          path: 'projects/:id?',
          component: NewProject,
          name: 'rprojects',
        },
        {
          path: 'courses/:id?',
          component: NewCourses,
          name: 'rcourses',
        },
        {
          path: 'awards/:id?',
          component: NewAwards,
          name: 'rawards',
        },
        {
          path: 'references/:id?',
          component: NewReferences,
          name: 'rreferences',
        },
        {
          path: 'organizations/:id?',
          component: NewOrganizations,
          name: 'rorganizations',
        },
        {
          path: 'publication/:id?',
          component: NewPublication,
          name: 'rpublication',
        },
        {
          path: 'declaration/:id?',
          component: NewDeclaration,
          name: 'rdeclaration',
        },
        {
          path: ':custom/:id?',
          component: NewCustom,
          name: 'rcustom',
        }
      ]
    },
    {
      path: '/workspace/:type?/design/:template?',
      component: WorkSpaceSection,
      children: [
        {
          path: '',
          component: Design,
          name: 'design'
        }
      ]
    },
    {
      path: '/workspace/:type?/finalize/:template?',
      component: WorkSpaceSection,
      children: [
        {
          path: '',
          component: Finalize,
          name: 'finalize'
        }
      ]
    },
    {
      path: '/workspace/website',
      name: 'website',
      component: Website
    },
    {
      path: '/check',
      name: 'check',
      component: Check
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile
    },
    {
      path: '/setting',
      name: 'setting',
      component: Setting
    }
  ]
})
