<template>
  <div class="pb-12 pr-5 pl-6">

    <div class="mt-12 flex flex-col space-y-4">
      <CardEditContent title="Create Skills" icon="/static/img/icons-primary/Star.svg">
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Skill Category Name
            <span class="text-button">*</span>
          </h1>
          <Input v-model="form.category" placeholder="Enter Skill Category Name" />
        </div>
        <div class="flex items-center gap-5" v-for="(skill, index) in form.skills" :key="index">
          <div class="w-full">
            <h1 class="font-medium text-primary-1 text-sm mb-2">Skill Name</h1>
            <Input v-model="skill.name" placeholder="Enter Skill Name" />
          </div>
          <div class="w-full">
            <h1 class="font-medium text-primary-1 text-sm mb-2">Skill Level</h1>
            <SkillDropdown
              :data="skillLevels"
              v-model="skill.level"
              v-on:valueSelect="selectSkill"
              v-on:valueOnInput="selectSkill"
              :index="index"
            >
              <template #skillLevel>
                <p v-if="isNaN(skill.level)"> {{ skill.level }} </p>
                <SkillLevel v-else :level="parseInt(skill.level)" :value="0" />
              </template>
            </SkillDropdown>
          </div>
          <div class="flex items-center gap-2">
            <svg
              @click="removeSkill(index)"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              class="fill-current text-red-500 cursor-pointer mt-6"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M14.5 3H9.5L8.5 4H5V6H19V4H15.5L14.5 3ZM16 9V19H8V9H16ZM6 7H18V19C18 20.1 17.1 21 16 21H8C6.90002 21 6 20.1 6 19V7Z"
              />
            </svg>
            <svg
              width="24"
              class="fill-current text-gray-600 cursor-pointer mt-6"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M3 8V6H21V8H3ZM3 13H21V11H3V13ZM3 18H21V16H3V18Z"
              />
            </svg>
          </div>
        </div>
        <button
          @click="addSkill"
          class="p-2 focus:outline-none text-button rounded w-full border-2 border-button border-dotted hover:bg-main-button hover:bg-opacity-25"
        >Add Skill</button>
      </CardEditContent>

      <div class="flex items-center space-x-5">
        <button
          @click="onCancel"
          class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
        >Cancel</button>
        <Button @click.native="onSubmit" value="Save" />
      </div>
    </div>
  </div>
</template>


<script>
import Button from '@/components/Ui/Button.vue'
import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Input from '@/components/Ui/Input.vue'
import SkillDropdown from '@/components/Resume/SkillDropdown.vue'
import SkillLevel from '@/components/Resume/SkillLevel'

export default {
  components: {
    Button,
    CardEditContent,
    Input,
    SkillDropdown,
    SkillLevel
  },
  data() {
    return {
      form: {},
      formBackup: '',
      index: -1,
      skillLevels: [
        'Elementary',
        'Limited',
        'Professional',
        'Full',
        'Native',
        'None'
      ],
    }
  },
  methods: {
    onCancel() {
      let form = JSON.parse(this.formBackup)
      for (const key in form) {
        this.form[key] = form[key]
      }
      this.$router.push('/workspace/resume/content')
    },
    onSubmit () {
      this.$store.dispatch('resumes/update')
      this.$router.push('/workspace/resume/content')
    },
    addSkill: function() {
      this.form.skills.push({ name: '', level: '' })
    },
    removeSkill: function(index) {
      this.form.skills.splice(index, 1)
    },
    selectSkill(value, index) {
      this.form.skills[index].level = value
    },
    clenSkills () {
      for (const index in this.form.skills) {
        if (!this.form.skills[index].name) {
          this.removeSkill(index)
        }
      }
    }
  },
  mounted() {
    this.index = this.$route.params.id
    this.form = this.$store.state.rcontent.content.addedContents.skills.data[this.index]
    this.formBackup = JSON.stringify(this.form)
    this.addSkill()
  },
  beforeDestroy () {
    this.clenSkills()
    if (!this.form.category) {
      this.$store.commit('rcontent/removeContentByIndex', {
        key: 'skills',
        index: this.index
      })
    }
  }
}
</script>
