<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p 
        v-if="!index"
        :style="sectionTitleStyle"
      >
        {{ content.titleAlias }}
      </p>
      <div style="display: flex; justify-content: space-between;">
        <div>
          <p style="padding-top: 6px"> 
            <strong style="font-weight: 600; color: #000">
              {{ data.category }}
            </strong>
            {{ skillString(data.skills) }}
          </p>
        </div> 
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle'],
  methods: {
    skillString (skillObj) {
      let skills = []
      for (const skill of skillObj) {
        if (skill.name.trim()) {
          skills.push(skill.name)
        }
      }

      if (skills.length) {
        return '(' + skills.join(', ') + ')'
      }
      return ''
    }
  },
  updated() {
    this.$emit('onUpdate')
  }
}
</script>