<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p 
        v-if="!index"
        :style="sectionTitleStyle"
      >
        {{ content.titleAlias }}
      </p>
      <div style="display: flex; padding-top: 12px;">
        <div v-if="data.date" style="width: 25%">
          <p style="color: #3E7A69; font-size: 16px;"> 
            {{ data.date }}
          </p>
        </div>
        <div :style="'width: ' + (data.date ? 75 : 100) + '%'">
          <p style="font-weight: 600;color: #000;font-size: 16px;margin-bottom: 6px;">
            {{ data.title }}
            {{ data.title && data.publisher ? ', ' : '' }}
            <span style="font-weight: 400;">
            {{ data.publisher }}
            </span>
          </p>
          <div class="ql-editor p-0" v-html="data.description"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle'],
  updated() {
    this.$emit('onUpdate')
  }
}
</script>