<template>
  <div class="pr-5 pl-6 pb-12">
    <div class="sticky top-0 bg-primary-2 z-50 pb-8">
      <h1 class="text-2xl font-bold text-primary-3">Dashboard</h1>
      <p class="text-primary-1">Your recent collections and informations</p>
    </div>
    <div class="px-6 sticky">
      <div class="flex space-x-8 border-b-2 border-gray-400 sticky bg-primary-2 z-50" style="top: 90px">
        <h1
          @click="handleChange('resume')"
          :class="[
            active == 'resume'
              ? 'border-button text-button'
              : 'border-transparent text-grayy'
          ]"
          class="cursor-pointer border-b-2 pb-3 mbb font-semibold text-base pl-4 pr-4"
        >
          Resume
        </h1>
        <h1
          @click="handleChange('cover')"
          :class="[
            active == 'cover'
              ? 'border-button text-button'
              : 'border-transparent text-grayy'
          ]"
          class="cursor-pointer border-b-2 pb-3 mbb font-semibold text-base pl-4 pr-4"
        >
          Cover Letter
        </h1>
        <h1
          @click="handleChange('website')"
          :class="[
            active == 'website'
              ? 'border-button text-button'
              : 'border-transparent text-grayy'
          ]"
          class="cursor-pointer border-b-2 pb-3 mbb font-semibold text-base pl-4 pr-4"
        >
          Website
        </h1>
      </div>
      <div
        v-if="active == 'resume'"
        class="grid grid-cols-4 -xl:grid-cols-3 -lg:grid-cols-2 -md:grid-cols-1 gap-8 mt-8"
      >
        <CardResume
          type="resume"
          icon="cv.png"
          v-for="resume in $store.state.resumes.resumes"
          :key="resume.resumeId"
          :name="resume.resumeName"
          :idx="resume.resumeId"
        />
        <div style="height: 450px" class="bg-white rounded p-6 flex flex-col">
          <div class="flex justify-center items-center flex-1">
            <h1 class="text-button">Create new resume</h1>
          </div>
          <div>
            <div @click="redirectUser('/workspace/resume/content', 'resume')">
              <button
                class="bg-main-button py-4 rounded w-full text-white focus:outline-none"
              >
                + Create New
              </button>
            </div>
            <button
              class="bg-main-button bg-opacity-25 py-3 border-2 rounded w-full mt-6 text-button border-button focus:outline-none"
            >
              Edit Sample
            </button>
          </div>
        </div>
      </div>
      <div
        v-if="active == 'cover'"
        class="grid grid-cols-4 -xl:grid-cols-3 -lg:grid-cols-2 -md:grid-cols-1 gap-8 mt-8"
      >
        <div style="height: 450px" class="bg-white rounded p-6 flex flex-col">
          <div class="flex justify-center items-center flex-1">
            <h1 class="text-button">Create new cover later</h1>
          </div>
          <div>
            <div @click="redirectUser('/workspace/coverletter/content', 'coverletter')">
              <button
                class="bg-main-button py-4 rounded w-full text-white focus:outline-none"
              >
                + Create New
              </button>
            </div>
            <button
              class="bg-main-button bg-opacity-25 py-3 border-2 rounded w-full mt-6 text-button border-button focus:outline-none"
            >
              Edit Sample
            </button>
          </div>
        </div>
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
        <CardResume
          edit="/workspace/coverletter/content"
          type="coverletter"
          icon="coverletter.png"
        />
      </div>
      <div v-if="active == 'website'" class="mt-8">
        <h1 class="text-3xl font-bold text-primary-1">Website</h1>
      </div>
    </div>
  </div>
</template>


<script>
import CardResume from '@/components/Dashboard/CardResume'

export default {
  components: {
    CardResume
  },
  data() {
    return {
      active: 'resume'
    }
  },
  methods: {
    handleChange: function (e) {
      this.active = e
      if (this.active == 'resume') {
        this.getResumes()
      }
    },
    getResumes () {
      this.$store.state.fullScreenProgress = this.$loading.show()
      this.$store.dispatch('resumes/getall')
      .then(() => {
        this.$store.state.fullScreenProgress.hide()
      })
    },
    redirectUser: function (route, type) {
      if (type === 'coverletter') {
        this.$store.commit('clcontent/resetCoverletterContent')
        this.$router.push(route)
      }
      if (type === 'resume') {
        this.$store.commit('rcontent/resetResumeContent')
        this.$store.commit('rcontent/addContent', 'experience')
        this.$store.commit('rcontent/addContent', 'education')
        this.$store.state.fullScreenProgress = this.$loading.show()
        this.$store.dispatch('resumes/create')
        .then((response) => {
          localStorage.selectedresume = response
          this.$store.state.fullScreenProgress.hide()
          this.$router.push(route)
        })
      }
    }
  },
  mounted() {
    this.getResumes()
  }
}
</script>

<style scoped>
.mbb {
  margin-bottom: -2px;
}
</style>
