async function htmltopdf () {

  let pagebreakClasses = document.querySelectorAll('.removepagebreak');

  pagebreakClasses.forEach(element => {
    element.style.display = 'none';
  });

  let content = "<html><head>" + 
  '<link rel="preconnect" href="https://fonts.gstatic.com">' +
  '<link href="https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=PT+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Source+Serif+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap" rel="stylesheet">' +
  '<link href="https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap" rel="stylesheet">' +
  "<style>html{line-height:1.5;-webkit-text-size-adjust:100%;margin:0px;}body{margin:0px;}*{box-sizing:border-box;border-width:0;border-style:solid;border-color:#e2e8f0}p{margin:0}svg{vertical-align:middle}</style>" + 
  "</head><body>";

  content += document.getElementById('pdfpreview').outerHTML;

  content += "</body></html>";

  pagebreakClasses = document.querySelectorAll('.removepagebreak');

  pagebreakClasses.forEach(element => {
    element.style.display = 'block';
  });

  var myHeaders = new Headers();
  myHeaders.append("Content-Type", "text/html");
  myHeaders.append("Accept", "application/pdf");

  var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: content,
  };
  return await (await fetch(process.env.VUE_APP_PDFAPI_URL + "/convert/htmltopdf", requestOptions)).arrayBuffer();
}

export const pdf = {
  htmltopdf,
};