<template>
  <div class="flex flex-wrap overflow-y-auto" id="modalscrollbar">
      <AddSectionRow
        v-for="content in contents"
        :key="content.id"
        :data="content"
        :index="content.id"
        class="w-full mx-8"
      >
        <h1 class="text-primary-1 text-base mb-2">{{ content.text }}</h1>
      </AddSectionRow>
  </div>
</template>


<script>
import AddSectionRow from '@/components/Resume/AddSectionRow.vue'

export default {
  name: 'AddContent',
  props: ['contentHeight'],
  components: {
    AddSectionRow
  },
  computed: {
    contents () {
      return this.$store.state.rcontent.content.contents
    },
  },
  beforeCreate() {
    this.$store.commit('rcontent/addCustomContent')
  }
}
</script>
