<template>
  <div>
    <div id="toolbar" slot="toolbar">
      <div class="mr-6">
        <button class="ql-bold">Bold</button>
        <button class="ql-underline">Underline</button>
        <button class="ql-italic">Italic</button>
      </div>
      <div class="mr-6">
        <button class="ql-list" value="ordered" type="button">ordered</button>
        <button class="ql-list" value="bullet" type="button">bullet</button>
        <!-- <button class="ql-list" value="check" type="button">check</button> -->
      </div>
      <!-- <div class="mr-6">
                <button class="ql-indent" value="-1">-1</button>
                <button class="ql-indent" value="+1">+1</button>
            </div> -->
      <!-- <div class="mr-6">
                <button class="ql-align" value="">left</button>
                <button class="ql-align" value="center">center</button>
                <button class="ql-align" value="right">right</button>
                <button class="ql-align" value="justify">justify</button>
            </div> -->
      <button class="ql-link">link</button>
    </div>
    <quill-editor
      :content="value"
      @change="onEditorChange($event)"
      ref="myQuillEditor"
      :options="editorOption"
    />
  </div>
</template>


<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'

import { quillEditor } from 'vue-quill-editor'
export default {
  name: 'TextEditor',
  props: ['value'],
  components: {
    quillEditor
  },
  data() {
    return {
      editorOption: {
        modules: {
          toolbar: '#toolbar'
        }
      },
      content: null
    }
  },
  methods: {
    // eslint-disable-next-line
    onEditorChange({ quill, html, text }) {
      this.$emit('editorChange', html)
      this.content = html
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill
    }
  }
}
</script>

<style>
.ql-toolbar.ql-snow {
  display: flex;
  align-items: center;
  align-content: center;
  padding: 15px 15px;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  border-color: #edf2f7;
  background-color: #fcfcfc;
}
.ql-snow.ql-toolbar button,
.ql-snow .ql-toolbar button {
  padding: 0px !important;
  width: 30px !important;
  height: auto !important;
}
.ql-container.ql-snow {
  height: 250px;
  border-color: #edf2f7;
  background-color: #edf2f7;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
}
.ql-container {
  font-size: 0.875rem;
  line-height: 1.5rem;
  font-family: Poppins, sans-serif;
  color: #4a5568;
}
</style>
