async function signup(data) {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");

  var raw = JSON.stringify(data);

  var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/auth/signup", requestOptions)).json();
}

async function signin(data) {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");

  var raw = JSON.stringify(data);

  var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/auth/login", requestOptions)).json();
}

async function me() {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");
  myHeaders.append("Authorization", "Bearer " + localStorage.token);

  var requestOptions = {
    method: 'GET',
    headers: myHeaders,
    redirect: 'follow'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/auth/user/me", requestOptions)).json();
}

export const auth = {
  signup, signin, me
};