
async function getall() {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");
  myHeaders.append("Authorization", "Bearer " + localStorage.token);

  var requestOptions = {
    mode: 'cors',
    method: 'GET',
    headers: myHeaders,
    redirect: 'follow',
    credentials: 'include'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/application-service/v1.0/resume", requestOptions)).json();
}

async function get(id) {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");
  myHeaders.append("Authorization", "Bearer " + localStorage.token);

  var requestOptions = {
    mode: 'cors',
    method: 'GET',
    headers: myHeaders,
    redirect: 'follow',
    credentials: 'include'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/application-service/v1.0/resume/" + id, requestOptions)).json();
}

async function create() {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");
  myHeaders.append("Authorization", "Bearer " + localStorage.token);

  var raw = JSON.stringify({
    resumeName: "my resume",
    htmlContent: "html preview content of the resume",
    templateId: 0,
    resumeJson: "this shold be the data array of the form sections."
  });

  var requestOptions = {
    mode: 'cors',
    method: 'POST',
    headers: myHeaders,
    body: raw,
    redirect: 'follow',
    credentials: 'include'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/application-service/v1.0/resume", requestOptions)).text();
}

async function update(payload) {

  var myHeaders = new Headers();

  myHeaders.append("Content-Type", "application/json");
  myHeaders.append("Authorization", "Bearer " + localStorage.token);

  var raw = JSON.stringify(payload);

  var requestOptions = {
    mode: 'cors',
    method: 'PUT',
    headers: myHeaders,
    body: raw,
    redirect: 'follow',
    credentials: 'include'
  };

  return await (await fetch(process.env.VUE_APP_API_URL + "/application-service/v1.0/resume", requestOptions)).text();
}

export const resume = {
    getall, get, create, update
};