<template>
  <div class="pb-12 pr-5 pl-6">

    <div class="mt-12 flex flex-col space-y-4">
      <CardEditContent title="Create Language" icon="/static/img/icons-primary/Language.svg">
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Language <span class="text-button">*</span>
          </h1>
          <Input v-model="form.language" placeholder="Enter language" rows="2" />
        </div>

        <div class="grid grid-cols-4 gap-5">
        </div>
        <div>
          <h1 class="font-semibold text-primary-1 text-base">
            Language Level
            <span class="float-right text-sm font-light text-button"
              >Beginner (A1, 0/0+)</span
            >
          </h1>
          <RangeSlider class="mt-4" />
        </div>
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Certificate
            <span class="float-right text-sm font-light text-primary-2"
              >(Recommended)</span
            >
          </h1>
          <div class="flex items-center gap-5 mb-2" v-for="(certificate, index) in form.certificates" :key="index">
            <Input v-model="certificate.value" placeholder="e.g. C2, 4+, TOEFL, IELTS e.t.c" class="flex-auto" />
            <svg
              @click="removeCertificate(index)"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              class="fill-current text-red-500 cursor-pointer"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M14.5 3H9.5L8.5 4H5V6H19V4H15.5L14.5 3ZM16 9V19H8V9H16ZM6 7H18V19C18 20.1 17.1 21 16 21H8C6.90002 21 6 20.1 6 19V7Z"
              />
            </svg>
          </div>
          <button
            @click="addCertificate"
            class="p-2 mt-2 focus:outline-none text-button rounded w-full border-2 border-button border-dotted hover:bg-main-button hover:bg-opacity-25"
          >Add Certificate</button>
        </div>

        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Description
            <span class="float-right text-sm font-light text-primary-2"
              >(Optional)</span
            >
          </h1>
          <TextEditor v-model="form.description" @editorChange="editorData" />
        </div>

        <!-- <button
          class="bg-main-button rounded w-full bg-opacity-10 text-button flex justify-center items-center focus:outline-none p-4 border-2 border-button"
        >
          Add More
          <img src="/static/img/icon/heroicons/plus-green.svg" class="w-6 h-6" />
        </button> -->
      </CardEditContent>

      <div class="flex items-center justify-end space-x-5">
        <button
          @click="onCancel"
          class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
        >
          Cancel
        </button>
        <Button @click.native="onSubmit" value="Save" />
      </div>
    </div>
  </div>
</template>


<script>
import Button from '@/components/Ui/Button.vue'
import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Input from '@/components/Ui/Input.vue'
import RangeSlider from '@/components/Ui/RangeSlider.vue'
import TextEditor from '@/components/Ui/TextEditor.vue'

export default {
  components: {
    Button,
    CardEditContent,
    Input,
    TextEditor,
    RangeSlider
  },
  data: function () {
    return {
      form: {},
      formBackup: '',
      index: -1,
    }
  },
  methods: {
    editorData (data) {
      this.form.description = data
    },
    onCancel() {
      let form = JSON.parse(this.formBackup)
      for (const key in form) {
        this.form[key] = form[key]
      }
      this.$router.push('/workspace/resume/content')
    },
    onSubmit () {
      this.$store.dispatch('resumes/update')
      this.$router.push('/workspace/resume/content')
    },
    addCertificate: function() {
      this.form.certificates.push({value: ''})
    },
    removeCertificate: function(index) {
      this.form.certificates.splice(index, 1)
    },
    clenCertificates () {
      for (const index in this.form.certificates) {
        if (!this.form.certificates[index].value) {
          this.removeCertificate(index)
        }
      }
    }
  },
  mounted() {
    this.index = this.$route.params.id
    this.form = this.$store.state.rcontent.content.addedContents.languages.data[this.index]
    this.formBackup = JSON.stringify(this.form)
    this.addCertificate()
  },
  beforeDestroy () {
    this.clenCertificates()
    if (!this.form.language) {
      this.$store.commit('rcontent/removeContentByIndex', {
        key: 'languages',
        index: this.index
      })
    }
  }
}
</script>
