<template>
  <div class="flex items-center">
    <input
      :value="val"
      v-model="checked"
      @change="onChange"
      class="checkbox"
      type="checkbox"
      :id="id"
      :name="name"
    />
    <label :for="id" class="label"></label>
    <label :for="id" class="cursor-pointer font-normal text-xs ml-3">
      {{ label }}
    </label>
  </div>
</template>

<script>
export default {
  name: 'Checkbox',
  props: ['label', 'name', 'id', 'value', 'val'],
  data() {
    return {
      checkedProxy: false
    }
  },
  computed: {
    checked: {
      get() {
        return this.value
      },
      set(val) {
        this.checkedProxy = val
      }
    }
  },
  methods: {
    onChange: function () {
      this.$emit('input', this.checkedProxy)
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
[type='checkbox']:checked,
[type='checkbox']:not(:checked){
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}
label {
    color: #8E8E8E;
}
.checkbox:checked + .label,
.checkbox:not(:checked) + .label {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16px;
  width: 16px;
  font-size: 12px;
  font-weight: 400;
  /* margin: 0 5px; */
  border-radius: 4px;
  cursor: pointer;
  color: #8E8E8E;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
  border: 1px solid $primary;
}
.checkbox:not(:checked) + .label {
  background-color: #fff;
  color: #8E8E8E;
  border: 1px solid #E5E5E5;
  box-sizing: border-box;
  border-radius: 2px;
}
.checkbox:checked + .label {
  background-color: $primary;
  color: #fff;
  background-image: url('__STATIC_PATH__/img/icon/checkbox-check.svg?ts=__VERSION__');
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center;
}

</style>
