<template>
  <Mockup v-if="isTemplate" />
  <div class="flex-1 flex w-full" v-else>
    <div class="page-content">
      <div class="pages" :style="previewCss">
        <component :is="template" v-if="!empty(template)" :content="content" />
        <br />
      </div>
    </div>
    <!-- <div class="mr-3 fixed right-0 bottom-0 p-3">
      <button
        v-if="$store.state.fullPreview"
        @click="$store.commit('fullPreviewMode', false)"
        class="p-3 bg-dark focus:outline-none text-white rounded-full border-1 border-transparent"
      >
        <img class="w-6" src="/static/img/icon/heroicons/zoomout-white.svg">
      </button>
      <button
        v-else
        @click="$store.commit('fullPreviewMode', true)"
        class="p-3 bg-dark focus:outline-none text-white rounded-full border-1 border-transparent"
      >
        <img class="w-6" src="/static/img/icon/heroicons/zoomin-white.svg">
      </button>
      <p></p>
      <button
        class="p-3 bg-dark focus:outline-none text-white rounded-full mt-3 border-1 border-transparent"
        @click="downloadPdf"
      >
        <img class="w-6" src="/static/img/icon/heroicons/download-white.svg">
      </button>
    </div> -->
  </div>
</template>

<script>
import Mockup from '@/mockup'
import cvcomponents from '@/template/coverletter'
import rcomponents from '@/template/resume'
import { empty } from '@/utils'
export default {
  components: {
    Mockup,
    ...cvcomponents,
    ...rcomponents
  },
  name: 'Preview',
  props: {
    isTemplate: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    empty,
    downloadPdf () {

      let filename = []

      if (this.$route.path.includes('coverletter')) {
        if (this.$store.state.clcontent.content.personalDetail.firstName) {
          filename.push(this.$store.state.clcontent.content.personalDetail.firstName)
        }
        if (this.$store.state.clcontent.content.personalDetail.lastName) {
          filename.push(this.$store.state.clcontent.content.personalDetail.lastName)
        }
      }

      if (this.$route.path.includes('resume')) {
        if (this.$store.state.rcontent.content.personal.firstname) {
          filename.push(this.$store.state.rcontent.content.personal.firstname)
        }
        if (this.$store.state.rcontent.content.personal.lastname) {
          filename.push(this.$store.state.rcontent.content.personal.lastname)
        }
      }

      if (filename.length) {
        filename = filename.join('_')
      } else {
        if (this.$route.path.includes('resume')) {
          filename = 'resume'
        }
        
        if (this.$route.path.includes('coverletter')) {
          filename = 'cv'
        }
      }
      this.$store.state.fullScreenProgress = this.$loading.show();
      this.$store.dispatch('main/downloadpdf', {
        filename: filename,
      }).then(() => {
        this.$store.state.fullScreenProgress.hide()
      })
    }
  },
  computed: {
    previewCss () {
      return this.$store.state.previewCss;
    },
    template () {
      return this.$store.state.main.previewtemplate;
    },
    content () {
      if (this.$route.path.includes('coverletter')) {
        return this.$store.state.clcontent.content;
      }
      return '';
    }
  },
  mounted: function () {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
    }, 150);
    if (this.$route.path.includes('coverletter') &&
      !this.$store.state.main.coverletters.includes(this.$store.state.main.previewtemplate)
    ) {
      this.$store.commit('main/setPreviewtemplate', 'contentLetter1')
    }
    if (this.$route.path.includes('resume') &&
      !this.$store.state.main.resumetemplates.includes(this.$store.state.main.previewtemplate)
    ) {
      this.$store.commit('main/setPreviewtemplate', 'resume1')
    }
  },
}
</script>
