
<template>
  <div>
    <VueSlideBar
      :value="defaultValue"
      :data="data"
      :range="range"
      :tooltipStyles="{
        backgroundColor: '#FFF',
        borderColor: '#FFF',
        'color':'#000',
        'font-size': '10px',
        'box-shadow': '0px 7px 16px rgba(0, 0, 0, 0.1)'
      }"
      :lineHeight='10'
      @callbackRange="callbackRange"
    />
  </div>
</template>

<script>
import VueSlideBar from 'vue-slide-bar'

export default {
  props: [
    'min',
    'max',
    'objectkey',
    'postfix',
    'defaultValue',
    'data',
    'range'
  ],
  data: () => ({
    value: 10
  }),
  methods: {
    callbackRange(val) {
      this.$store.commit('cldesign/setDesignByKey', {
        key: this.objectkey,
        value: val.label
      })
    }
  },
  computed: {},
  created: function () {
  },

  components: {
    VueSlideBar
  }
}
</script>

