<template>
  <div>
    <div v-if="$route.params.template">
        <TemplateContainer :type="type" />
    </div>
    <div v-else>
      <div v-if="$route.params.type == 'resume'">
        <DesignResume />
      </div>
      <div v-if="$route.params.type == 'coverletter'">
        <DesignCoverLetter />
      </div>
    </div>
  </div>
</template>
<script>
import TemplateContainer from '@/components/Design/TemplateContainer.vue'
import DesignResume from '@/components/Design/DesignResume.vue'
import DesignCoverLetter from '@/components/Design/DesignCoverLetter.vue'
export default {
  components: {
    DesignResume,
    DesignCoverLetter,
    TemplateContainer
  },
  computed: {
    type () {
      let type = ''
      if (this.$route.params.type == 'coverletter') {
        type = 'coverletter'
      }
      if (this.$route.params.type == 'resume') {
        type = 'resume'
      }

      return type
    }
  }
}
</script>
