<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p v-if="!index" :style="sectionTitleStyle">
        <template v-for="(chunk, idx) in titleAliasChunks">
          <span :key="idx" style="font-size: 22px;">{{ chunk.first }}</span>{{ chunk.rest }} 
        </template>
      </p>
      <div style="display: flex;justify-content: space-between;padding-top: 10px">
        <div>
          <p style="font-size: 20px;font-weight: 600;">
            {{ data.name }}
          </p>
          <p style="font-size: 15px;color: #848484;"><em>{{ data.organization }}</em></p>
        </div>
        <div>
          <p style="font-size: 17px;font-weight: 600;"> 
            {{ data.startDate }}
            {{ data.startDate && data.endDate ? '-' : '' }}
            {{ data.endDate }}
          </p>
        </div>
      </div>
      <div v-if="data.description" style="padding-top: 8px">
        <div class="ql-editor p-0" style="border-top: 3px solid #F2F2F2;padding-top: 10px" 
        v-html="data.description"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle', 'titleAliasChunks'],
  updated() {
    this.$emit('onUpdate')
  }
}
</script>