<template>
  <div
    class="w-full py-3 px-3  text-gray-700 focus-within:bg-white border border-gray-400 focus-within:border-teal-600 rounded flex items-center"
  >
    <input
      v-bind:value="value"
      v-on:input="$emit('input', $event.target.value)"
      :placeholder="placeholder"
      type="text"
      class="focus:outline-none text-sm bg-transparent w-full pr-3"
    />
    <img :src="icon" class="w-5" />
  </div>
</template>

<script>
export default {
  name: 'InputIcon',
  props: ['icon', 'placeholder', 'value']
}
</script>
