<template>
  <div class="space-y-1">
    <label id="listbox-label" class="block text-sm leading-5 font-medium text-gray-700">
      <slot name="SelectLabel"></slot>
    </label>

    <div class="relative" v-click-outside="closeDropdown">
      <button
        type="button"
        @click="openDropdown"
        class="w-full py-3 px-3 text-gray-700 bg-gray-200 border-2 border-gray-200 rounded focus:outline-none focus:bg-white focus:border-teal-600 text-left"
      >
        <div v-if="value !== ''" class="flex items-center space-x-3">
          <slot name="skillLevel"></slot>
        </div>
        <div v-if="value == ''">
          <span class="text-gray-500">Enter a skill level</span>
        </div>
        <span class="absolute inset-y-0 right-0 flex items-center pr-2 pointer-events-none">
          <img src="/static/img/icon/heroicons/chevron-up.svg" :class="{ 'rotate-180': isOpen }" class="transform w-4" />
        </span>
      </button>
      <div v-show="isOpen" class="absolute mt-1 w-full rounded-md bg-white shadow-lg z-50">
        <Input placeholder="Custom name" class="m-5" v-on:input="changeVal"/>
        <ul
          class="max-h-56 rounded-md py-1 text-base leading-6 shadow-xs overflow-auto focus:outline-none sm:text-sm sm:leading-5"
        >
          <li
            @click="select(d)"
            id="listbox-item-0"
            role="option"
            v-for="d in data"
            :key="d"
            class="text-gray-900 cursor-default select-none relative py-2 pl-3 pr-9 cursor-pointer hover:text-white hover:bg-main-button focus:outline-none focus:text-white focus:bg-main-button"
          >
            <div class="flex items-center space-x-3">
              <span
                class="block truncate"
                :class="{ 'font-normal' : !isSelected(d) , 'font-semibold' : isSelected(d)}"
              >{{ d }}</span>
            </div>
            <span v-show="isSelected(d)" class="absolute inset-y-0 right-0 flex items-center pr-4">
              <svg class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                <path
                  fill-rule="evenodd"
                  d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                  clip-rule="evenodd"
                />
              </svg>
            </span>
          </li>
          <hr />
          <li
            @click="select(n)"
            role="option"
            v-for="n in 5"
            :key="n"
            class="text-gray-900 cursor-default select-none relative py-2 pl-3 pr-9 cursor-pointer hover:text-white hover:bg-main-button focus:outline-none focus:text-white focus:bg-main-button"
          >
            <SkillLevel :level="n" :value="parseInt(value)" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import ClickOutside from 'vue-click-outside'
import Input from '@/components/Ui/Input.vue'
import SkillLevel from '@/components/Resume/SkillLevel.vue'

export default {
  name: 'SkillDropdown',
  props: {
    data: Array,
    value: {
      type: [String, Number],
      default: 'Hello'
    },
    index: Number
  },
  data() {
    return {
      isOpen: false
    }
  },
  components: {
    Input,
    SkillLevel
  },
  methods: {
    isSelected(value) {
      return this.value === value
    },
    closeDropdown() {
      this.isOpen = false
    },
    openDropdown() {
      this.isOpen = true
    },
    select(value) {
      this.isOpen = false
      this.$emit('valueSelect', value, this.index)
    },
    changeVal(value) {
      this.$emit('valueOnInput', value, this.index)
    }
  },
  directives: {
    ClickOutside
  }
}
</script>
