<template>
  <div :class="customclass">
    <div v-for="item in items" :key="item" :style="{ '--color': item }">
      <input
        class="checkbox-tools"
        type="radio"
        :name="objectkey"
        :id="objectkey + '-' + item"
        @click="setColorDefault(objectkey, item)"
        :checked="item == selectColor"
      />

      <label
        :class="
          'for-checkbox-tools ' +
          (item == '#FFFFFF' ? 'black-border' : 'no-border')
        "
        :for="objectkey + '-' + item"
      >
      </label>
    </div>

    <!--Custom color component  $store.commit('cldesign/setDesignByKey', {
            key: objectkey,
            value: item
          }) -->
    <verte
      picker="square"
      model="hex"
      menuPosition="bottom"
      @input="setColor"
      value="conic-gradient(from 90deg, violet, indigo, blue, green, yellow, orange, red, violet)"
    >
      <div :class="divStyle" :style="backGroundColor">
        <input
          class="checkbox-tools"
          type="radio"
          :id="objectkey + 'key'"
          :name="objectkey"
          :checked="customClicked"
        />
        <label
          :class="
            'for-checkbox-tools ' +
            ('#FFFFFFs' == '#FFFFFF' ? 'black-border' : 'no-border')
          "
          :for="objectkey"
        >
        </label>
      </div>
    </verte>
  </div>
</template>

<script>
import { get } from 'lodash'
import Verte from 'verte'
import 'verte/dist/verte.css' //This file has changed should be push to repository or need to upload the node module into separate repo

export default {
  name: 'RadioColorsGroup',
  props: ['customclass', 'items', 'objectkey', 'defaultValue'],
  components: { Verte },
  data: () => ({
    selectColor: '#000000',
    divStyle: 'custom-shape',
    customClicked: false,
    backGroundColor: {
      background:
        'conic-gradient(from 90deg, violet, indigo, blue, green, yellow, orange, red, violet)'
    }
  }),
  methods: {
    setColor: function (color) {
      this.selectColor = color
      this.backGroundColor['background'] = color
      this.$store.commit('cldesign/setDesignByKey', {
        key: this.objectkey,
        value: color
      })
      this.customClicked = true
    },
    setColorDefault: function (objectkey, color) {
      this.selectColor = color

      this.$store.commit('cldesign/setDesignByKey', {
        key: objectkey,
        value: color
      })
      this.customClicked = false
    }
  },
  computed: {
    getColor() {
      return get(...this.selectColor)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
[type='radio']:checked,
[type='radio']:not(:checked) {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}
.checkbox-tools:checked + label,
.checkbox-tools:not(:checked) + label {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 33px;
  width: 33px;
  font-size: 12px;
  font-weight: 400;
  margin: 0 auto;
  border-radius: 40px;
  cursor: pointer;
  color: $primary;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
.checkbox-tools:not(:checked) + label {
  background: var(--color);
  color: #686c70;
  /* border: 2px solid #686C70; */
}
.checkbox-tools:checked + label {
  background: var(--color);
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
}
.checkbox-tools:checked + .no-border {
  background-image: url('/static/img/icon/heroicons/check-white.svg?v=3');
}
.checkbox-tools:checked + .black-border {
  background-image: url('/static/img/icon/heroicons/check.svg?v=3');
}
.black-border {
  border: black solid 2px;
}

.custom-shape {
  width: 32px;
  height: 32px;
  color: white;
  font-size: 1.8rem;
  border-radius: 50%;
  cursor: pointer;
  font-weight: bold;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;

  border: none;
}
.custom-color {
  background: conic-gradient(
    from 90deg,
    violet,
    indigo,
    blue,
    green,
    yellow,
    orange,
    red,
    violet
  );
}
</style>
