<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p 
        v-if="!index"
        :style="sectionTitleStyle"
      >
        {{ content.titleAlias }}
      </p>
      <div style="padding-top: 12px;">
        <p style="font-weight: 600;color: #000;font-size: 16px;margin-bottom: 6px;">
          {{ data.language }}
          {{ data.language && certString(data.certificates) ? ', ' : '' }}
          <span style="font-weight: 400;">
          {{ certString(data.certificates) }}
          </span>
        </p>
        <div class="ql-editor p-0" v-html="data.description"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle'],
  updated() {
    this.$emit('onUpdate')
  },
  methods: {
    certString (certobj) {
      let skills = []
      for (const skill of certobj) {
        if (skill.value.trim()) {
          skills.push(skill.value)
        }
      }

      if (skills.length) {
        return skills.join(', ')
      }
      return ''
    }
  }
}
</script>