<template>
  <div :style="cssProps">
    <input
      class="checkbox-tools"
      type="radio"
      :name="name"
      :id="id"
      isChecked
    />
    <label class="for-checkbox-tools" :for="id"></label>
  </div>
</template>

<script>
export default {
  name: 'Radio',
  props: ['name', 'id', 'isChecked', 'color'],
  computed: {
    cssProps() {
      return {
        '--color': this.color
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
[type='radio']:checked,
[type='radio']:not(:checked) {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}
.checkbox-tools:checked + label,
.checkbox-tools:not(:checked) + label {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 33px;
  width: 33px;
  font-size: 12px;
  font-weight: 400;
  margin: 0 auto;
  border-radius: 5px;
  cursor: pointer;
  color: $primary;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
.checkbox-tools:not(:checked) + label {
  background: var(--color);
  color: #686c70;
  /* border: 2px solid #686C70; */
}
.checkbox-tools:checked + label {
  background: var(--color);
  background-image: url('__STATIC_PATH__/img/icon/design/ico-checklist.svg?ts=__VERSION__');
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
}
</style>
