<template>
<div class="relative appearance-none label-floating">
    <input class="w-full py-2 px-3 leading-normal rounded input-text" 
      :id="getID" 
      type="text" 
      :placeholder="placeholder"
      v-bind:value="value"
      v-on:input="$emit('input', $event.target.value)"
      :class="{ 'border-danger': error }"
      >
    <label class="absolute top-0 block pin-t pin-l w-full px-3 py-2 leading-normal" :for="getID">
     {{placeholder}}
    </label>
    <span class="text-danger text-sm" v-show="error">{{ error }}</span>
</div>
  <!-- <div>
    <input
      type="text"
      :placeholder="placeholder"
      v-bind:value="value"
      v-on:input="$emit('input', $event.target.value)"
      class="w-full py-3 px-3 text-sm text-gray-700  border border-gray-400 rounded focus:outline-none focus:bg-white focus:border-2 focus:border-teal-600"
      :class="{ 'border-danger': error }"
    />
    <span class="text-danger text-sm" v-show="error">{{ error }}</span>
  </div> -->
</template>

<script>
export default {
  name: 'Input',
  props: ['value', 'placeholder', 'error'],
  computed: {
    getID() {
      return this.placeholder?this.placeholder.replace(/[^A-Z0-9]/ig, "_"):`random-${Math.round(Math.random())}`;
    }
  }
}
</script>
