import { ALIGNS, FONTFAMILIES, COLUMNLAYOUTS } from './constant'
import TemplateContainer from './TemplateContainer.vue'
import ThemeCustomization from './ThemeCustomization.vue'
export default {
  components: {
    CardDesign,
    Radio,
    RadioColors,
    Checkbox,
    RangeSlider,
    RadioGroup,
    Select,
    Toggle,
    TemplateContainer,
    ThemeCustomization
  },
  data: () => ({
    textaligns: ALIGNS,
    fontfamilies: FONTFAMILIES,
    columnlayouts: COLUMNLAYOUTS,
    showAdvanceDesign: false
  }),
  methods: {
    updateFontfamily(value) {
      console.log('font family', value)
    },
  }
}
</script>
