<template>
<div class="relative">
  <div class="p-5 py-8 space-y-5">
    <!-- <div class="flex items-end justify-between">
      <div>
        <h1 class="text-2xl font-bold text-primary-3">Content</h1>
        <p class="text-primary-1">Fill up your personal information below</p>
      </div>
      <div class="w-50p pr-6 flex items-center relative">
        <input
          v-model="filename"
          type="text"
          id="filename"
          class="bg-white p-3 border-gray-400 rounded focus:outline-none border w-full focus:border-button"
          placeholder="Title"
        />
        <button
          class="p-2 bg-dark focus:outline-none text-white rounded ml-3 border-1 border-transparent"
        >
          <img
            class="w-10"
            src="/static/img/icon/heroicons/download-white.svg"
          />
        </button>
      </div>
    </div> -->
    <ResumeInformation title="Resume Information" 
      icon="/static/img/icons-primary/Document.svg"
      description="Lorem ipsum dolor sit amet, consectetur"/>

    <PersonalInformation title="Personal Information" 
      icon="/static/img/icons-primary/user.svg"
      description="Lorem ipsum dolor sit amet, consectetur"/>

    <ProfileDescription title="Add Profile" 
      icon="/static/img/icons-primary/Identification.svg"
      description="Lorem ipsum dolor sit amet, consectetur"
      :section="profile"/>

    <!-- <PreviewPersonal :personal="personal"/> -->
    <!-- <NewCardProfile
      :section="profile"
    /> -->
    <NewCardContent
      v-for="content in addedContents"
      :key="content.id"
      :section="content"
    />
    <div id="app">
      <!-- <div>
        <button
          type="button"
          class="bg-main-button rounded py-4 w-full mt-10 text-white focus:outline-none hover:bg-opacity-75 flex justify-center items-center"
          @click="show"
        >
          <img src="/static/img/icon/heroicons/plus-white.svg" class="w-6 h-6 mr-2" />Add Content
        </button>
      </div> -->
      <div class="bg-white rounded card-shadows px-5 cursor-pointer text-primary-3  on-hover" @click="show">
        <div class="flex space-x-4 py-5 items-center justify-center">
          <!-- <img src="/static/img/icons-primary/icon-plus.png"> -->
          <iconly name="paperplus" type="light" :color="'#2AAF8F'" class="w-8 h-8"/>
          <div class="flex-col">
            <h1 class="text-lg font-semibold">Add Section</h1>
          </div>
        </div>
      </div>
      <modal @opened="setMaxHeight" :height="'80%'" id="content-modal" :adaptive="true" name="AddContentModal" classes="overflow-hidden relative">
        <div class="w-100p mx-auto">
          <div class="modal-c-header p-5 py-8">
            <div class="flex items-center">
              <div class="w-full text-center">
                <h1
                  class="font-semibold text-primary-3 text-xl md:-mb-2 mb-0"
                >Add New Content</h1>
              </div>
              <div class="text-right">
                <img src="/static/img/icon/heroicons/close.svg" 
                  class="w-5 h-5 ml-auto cursor-pointer"
                  @click="hide"
                >
              </div>
            </div>
          </div>
          <AddContent :style="{maxHeight: contentHeight + 'px'}" />
        </div>
      </modal>
    </div>
  </div>
</div>
</template>

<script>
import NewCardContent from '@/components/Resume/NewCardContent.vue'
// import PreviewPersonal from '@/components/Resume/PreviewContent/PreviewPersonal.vue'
import AddContent from '@/components/Resume/AddContent.vue'
// import NewCardProfile from '@/components/Resume/NewCardProfile.vue'
import ResumeInformation from '@/components/Resume/ResumeInformation.vue'
import PersonalInformation from '@/components/Resume/PersonalInformation.vue'
import ProfileDescription from '@/components/Resume/ProfileDescription.vue'
import { eventBus } from '@/main'

export default {
  components: {
    // PreviewPersonal,
    NewCardContent,
    AddContent,
    // NewCardProfile,
    ResumeInformation,
    PersonalInformation,
    ProfileDescription
  },
  data() {
    return {
      skills: this.$store.state.resumeSkills,
      contentHeight: 0
    }
  },
  computed: {
    filename: function () {
      let filename = []
      if (this.$store.state.rcontent.content.personal.firstname.trim()) {
        filename.push(this.$store.state.rcontent.content.personal.firstname)
      }
      if (this.$store.state.rcontent.content.personal.lastname.trim()) {
        filename.push(this.$store.state.rcontent.content.personal.lastname)
      }
      return filename.join(' ')
    },
    showHideAddBtn() {
      if (this.skills.length > 0) {
        return 'right: 0;'
      } else {
        return 'right: -140px;'
      }
    },
    addedContents () {
      return this.$store.state.rcontent.content.addedContents
    },
    personal () {
      return this.$store.state.rcontent.content.personal
    },
    profile () {
      return this.$store.state.rcontent.content.profile
    }
  },
  methods: {
    show() {
      this.$modal.show('AddContentModal')
    },
    hide() {
      this.$modal.hide('AddContentModal')
    },
    setMaxHeight() {
      this.contentHeight = document.getElementById('content-modal').getElementsByClassName('vm--modal')[0].clientHeight - 85
    }
  },
  mounted() {
    this.hide()

    eventBus.$on('close-dialog', () => {
      this.hide()
    })

    if (localStorage.selectedresume && 
      this.$store.state.user.loggedIn &&
      this.$store.state.resumes.cachedresume != localStorage.selectedresume) {
        this.$store.state.fullScreenProgress = this.$loading.show()
        this.$store.dispatch('resumes/get', localStorage.selectedresume)
        .then((response) => {
          let resumeData = {};
          try {
            resumeData = JSON.parse(response.resumeJson)
          }
          catch(err) {
            resumeData = {};
          }
          if ('content' in resumeData) {
            this.$store.commit('rcontent/setResumeContent', resumeData.content)
          }
          this.$store.commit('main/setPreviewtemplate', this.$store.state.main.resumetemplates[parseInt(response.templateId)])
          this.$store.state.fullScreenProgress.hide()
        }) 
    }
  },
}
</script>

<style lang="scss">
.add-section-btn {
  background: #0b7769;
  color: #fff;
  outline: none !important;
  box-shadow: none !important;
  border: 0;
  position: relative;
  transition: all 0.4s cubic-bezier(1, 0.25, 0, 0.75) 0s;
  &:focus {
    outline: none !important;
    box-shadow: none !important;
  }
}
</style>
