<template>
  <label
    :for="id"
    class="flex items-center cursor-pointer p-2 gap-3"
  >
    <!-- label -->
    <div class="font-medium text-primary-1 text-sm">{{ label ? label : 'Show' }}</div>
    <!-- toggle -->
    <div class="relative">
      <!-- input -->
      <input
        :id="id"
        :value="val"
        v-model="checked"
        @change="onChange"
        type="checkbox"
        class="hidden"
      />
      <!-- line -->
      <div
        class="toggle__line w-16 tgh1 bg-gray-400 rounded-full shadow-inner"
      ></div>
      <!-- dot -->
      <div
        class="toggle__dot absolute w-6 tgh2 bg-white rounded-full shadow"
      ></div>
    </div>
  </label>
</template>

<script>
export default {
  name: 'MySwitch',
  props: ['id', 'value', 'val', 'label'],
  data() {
    return {
      checkedProxy: false
    }
  },
  computed: {
    checked: {
      get() {
        return this.value
      },
      set(val) {
        this.checkedProxy = val
      }
    }
  },
  methods: {
    onChange: function () {
      this.$emit('input', this.checkedProxy)
    }
  }
}
</script>

<style>
.toggle__dot {
  top: 3px;
  left: 5px;
  transition: all 0.3s ease-in-out;
}
.tgh1{
  height: 1.8rem;
}
.tgh2{
  height: 1.45rem;
}
input:checked ~ .toggle__dot {
  transform: translateX(125%);
  /*   background-color: #48bb78; */
}

input:checked ~ .toggle__line {
  background-color: #0e766b;
}
input:checked ~ .box {
  border-color: #0e766b !important;
}
.toggle__line {
  transition: all 0.3s ease-in-out;
}
</style>
