import Vue from 'vue'
import PortalVue from 'portal-vue'
import VModal from 'vue-js-modal'
import App from './App.vue'
import router from './router'
import store from './store/index'
import * as filters from '@/filters' // global filters
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'vue2-datepicker/index.css';
import 'vue-advanced-cropper/dist/style.css';
import SimpleVueValidation from 'simple-vue-validator';
// import './assets/index.css'
import './assets/scss/main.scss';

// Plugins
import './plugins/globalComponents';
import './plugins/slickCarousel';

export const eventBus = new Vue();

Vue.config.productionTip = false

Vue.use(VModal)
Vue.use(PortalVue)
Vue.use(Loading, {color: '#0e766b'},{})
Vue.use(SimpleVueValidation);
Vue.directive('imgSrc', {
  inserted: function(el, binding) {
    el.src = `/static/img/${binding.value}?ts=_185343_20210712`
  },
  update: function(el, binding) {
    el.src = `/static/img/${binding.value}?ts=_185343_20210712`
  }
})
// register global utility filters.
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

// router navigation gaurds
router.beforeEach((to, from, next) => {
  if (localStorage.token && !store.state.user.loggedIn) {
    store.dispatch('user/me')
    .then(() => {
      next()
    })
  } else {
    if (!store.state.user.loggedIn) {
      if (to.name == 'dashboard') {
        next('/')
      }
    }
    next()
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
