<script>
import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Input from '@/components/Ui/Input.vue'
import InputIcon from '@/components/Ui/InputIcon.vue'
import TextEditor from '@/components/Ui/TextEditor.vue'
import VueTailwindPicker from 'vue-tailwind-picker'
import { get, set, isObject } from 'lodash'
import { empty } from '@/utils'
import dayjs from 'dayjs'
import CustomDropdown from '@/components/Ui/CustomDropdown.vue'
import CountrySelect from '@/components/Ui/CountrySelect.vue'


export default {
  components: {
    CardEditContent,
    Input,
    InputIcon,
    TextEditor,
    VueTailwindPicker,
    CustomDropdown,
    CountrySelect
  },
  data() {
    return {
      data: {},
      toggleClosingFields: true,
      toggleEmployer: false,
      toggleLetter: false,
      togglePersonal: false,
      customClosingText: '',
      closings: ['Sincerely', 'Regards', 'Yours Truly'],
      desc: ''
    }
  },
  methods: {
    isObject,
    empty,
    checkValid(configPath) {
      return get(this.config, configPath, false)
    },
    toggleFields() {
      this.toggleClosingFields = !this.toggleClosingFields
    },
    personalNext() {
      this.togglePersonal = false
      this.toggleEmployer = true
    },
    employerNext() {
      this.toggleEmployer = false
      this.toggleLetter = true
    },
    updatePreview() {
      this.$store.commit('clcontent/updateCoverLetterOnFly', this.data)
    },
    updateClosing(value) {
      this.data.letterContent.closing = value
      this.updatePreview()
    },
    changeDate(value) {
      this.data.letterContent.date = dayjs(value).format('MMMM D, YYYY')
      this.$store.commit('clcontent/updateCoverLetterOnFly', this.data)
    },
    editorData(data) {
      this.data.letterContent.description = data
      this.updatePreview()
    }
  },
  computed: {
    filename: function () {
      let filename = []
      if (!empty(this.data)) {
        if (this.data.personalDetail.firstName.trim()) {
          filename.push(this.data.personalDetail.firstName)
        }
        if (this.data.personalDetail.lastName.trim()) {
          filename.push(this.data.personalDetail.lastName)
        }
      }
      return filename.join(' ')
    },
    covername: function () {
      let filename = []
      if (!empty(this.data)) {
        if (this.data.personalDetail.firstName.trim()) {
          filename.push(this.data.personalDetail.firstName)
        }
        if (this.data.personalDetail.lastName.trim()) {
          filename.push(this.data.personalDetail.lastName)
        }
      }
      return filename.join(' ')
    }
  },
  mounted() {
    const dataLetter = this.$store.state.clcontent.content
    if (empty(dataLetter.employerDetail.companyAddress)) {
      set(dataLetter, 'employerDetail.companyAddress', {})
    }

    if (this.$store.state.main.dashboardName) {
      let name = this.$store.state.main.dashboardName.split(' ')
      dataLetter.personalDetail.firstName = name[0]
      if (name.length > 1) {
        dataLetter.personalDetail.lastName = name[1]
      }
      this.$store.commit('main/updateDashboardName', '')
    }
    this.data = dataLetter
    this.updatePreview()
  }
}
</script>
