<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p 
        v-if="!index"
        :style="sectionTitleStyle"
      >
        {{ content.titleAlias }}
      </p>
      <div style="display: flex; justify-content: space-between;">
        <div>
          <p style="padding: 6px 0"> 
            <strong style="font-weight: 600; color: #000">
              {{ data.certificate }}
            </strong>
              {{ data.authority }}
          </p>
          <div
            style="font-size: 14px; padding-bottom: 6px"
            v-html="data.description"
            class="ql-editor px-0 pt-0"
          ></div>
        </div> 
        <div style="text-align: right; font-size: 14px; padding: 6px 0">
          <p> {{ data.date }} </p>
        </div>  
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle'],
  updated() {
    this.$emit('onUpdate')
  }
}
</script>