<template>
  <div class="bg-white rounded card-shadows" :class="[additionalClass && additionalClass.includes('no-padding')?'px-0':'px-5']">
    <div class="flex space-x-4 border-b py-5 items-center" :class="[additionalClass && additionalClass.includes('no-padding')?'px-5':'px-0']">
      <img :src="icon" class="h-8">
      <div class="flex-col">
        <h1 class="text-primary-1 text-lg font-semibold">{{ title }}</h1>
        <p class="text-primary-2 text-xs">{{description}}</p>
      </div>
    </div>
    <div :class="['flex flex-col space-y-5 py-5 pb-10', newClass]">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CardDesign',
  props: {
    title: {
      type: String,
      required: false,
      default: ''
    }, 
    icon: {
      type: String,
      required: false,
      default: ''
    }, 
    newClass: {
      type: String,
      required: false,
      default: ''
    }, 
    description: {
      type: String,
      required: false,
      default: ''
    }, 
    additionalClass: {
      type: String,
      required: false,
      default: ''
    }
  }
}
</script>
