import { isObject, isUndefined, isNull, isEmpty } from 'lodash'
import store from '@/store'

function empty(obj) {
  if (isUndefined(obj) || isNull(obj)) {
    return true
  }
  if (isObject(obj)) {
    return isEmpty(obj)
  }
  return obj === ''
}

function debounce(func, wait, immediate) {
	let timeout;
	return function() {
		let context = this, args = arguments,
            later = function() {
			timeout = null;
			if (!immediate) func.apply(context, args);
		};
		let callNow = immediate && !timeout;
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) func.apply(context, args);
	};
}

function pagebreak (pageHeight, tbmargin, pagebreakfix) {

  document.querySelectorAll('.removediv').forEach(e => e.remove());

	let height = pageHeight;
  let stepheight = tbmargin;
  let pagebreakHeight= 16;
  let totalPage = 1;

  for (const child of document.getElementById("pdfpreview").children) {
    if (stepheight + child.offsetHeight > height) {
      for (const perachild of child.children) {
        if (stepheight + perachild.offsetHeight > height) {
          let availheight = height - stepheight + tbmargin
          perachild.insertAdjacentHTML(
            'beforeBegin',
            '<div class="removediv" style="height: ' + availheight + 'px"></div>'
          )
          perachild.insertAdjacentHTML(
            'beforeBegin',
            '<div class="removediv removepagebreak" style="' + 
              'height: ' + pagebreakHeight +'px;' + 
              'margin: ' + 0 + 'px -' + pagebreakfix + 'px;' +
              'background-color: rgba(215, 218, 220, var(--bg-opacity));' +
              'box-shadow: inset 0px 0px 5px rgb(0 0 0 / 40%);' +
              'page-break-after: always;' +
            '"></div>'
          )
          perachild.insertAdjacentHTML(
            'beforeBegin',
            '<div class="removediv" style="height: ' + tbmargin + 'px"></div>'
          )
          height += pageHeight + pagebreakHeight
          stepheight += availheight
          totalPage++;
        } else  {
          stepheight += perachild.offsetHeight
        }
      }
    } else {
      stepheight += child.offsetHeight
    }
  }
  document.getElementById("pdfpreview").style.height = height + 'px'
  store.commit('updateTotalPreviewPages', totalPage)
}

function cloneobject (obj) {
  return JSON.parse(JSON.stringify(obj))
}

export {empty, debounce, pagebreak, cloneobject};