<template>
  <div class="pb-12 pr-5 pl-6">

    <div class="mt-12 flex flex-col space-y-4">
      <CardEditContent title="Create Reference" icon="/static/img/icons-primary/2User.svg">
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Full Name <span class="text-button">*</span>
          </h1>
          <InputIcon
            icon="/static/img/icons-primary/2User.svg"
            placeholder="Enter first and last name"
            v-model="form.name"
          />
        </div>

        <div class="grid grid-cols-2 gap-x-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Job Title
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.jobtitle" placeholder="Enter Job Title" />
          </div>
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Organization
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.organization" placeholder="Enter Organization" />
          </div>
        </div>

        <div class="grid grid-cols-2 gap-x-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Email
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.email" placeholder="Enter Email" />
          </div>
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Phone
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.phone" placeholder="Enter Phone" />
          </div>
        </div>
      </CardEditContent>

      <div class="flex items-center justify-end space-x-5">
        <button
          @click="onCancel"
          class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
        >
          Cancel
        </button>
        <Button @click.native="onSubmit" value="Save" />
      </div>
    </div>
  </div>
</template>


<script>
import Button from '@/components/Ui/Button.vue'
import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Input from '@/components/Ui/Input.vue'
import InputIcon from '@/components/Ui/InputIcon.vue'
export default {
  components: {
    Button,
    CardEditContent,
    Input,
    InputIcon
  },
  data: function () {
    return {
      form: {},
      formBackup: '',
      index: -1,
    }
  },
  methods: {
    onCancel() {
      let form = JSON.parse(this.formBackup)
      for (const key in form) {
        this.form[key] = form[key]
      }
      this.$router.push('/workspace/resume/content')
    },
    onSubmit () {
      this.$store.dispatch('resumes/update')
      this.$router.push('/workspace/resume/content')
    }
  },
  mounted() {
    this.index = this.$route.params.id
    this.form = this.$store.state.rcontent.content.addedContents.references.data[this.index]
    this.formBackup = JSON.stringify(this.form)
  },
  beforeDestroy () {
    if (!this.form.name) {
      this.$store.commit('rcontent/removeContentByIndex', {
        key: 'references',
        index: this.index
      })
    }
  }
}
</script>
