<template>
  <div :class="customclass">
    <div v-for="item in items" :key="item.label">
      <input
        class="radiogroup-tools"
        type="radio"
        :name="objectkey"
        :id="objectkey + '-' + item.value"
        @click="
          $store.commit('cldesign/setDesignByKey', {
            key: objectkey,
            value: item.value
          })
        "
        :checked="item.value == defaultValue"
      />
      <label class="for-radiogroup-tools" :for="objectkey + '-' + item.value">
        <simple-svg
          v-if="item.icon"
          :src="item.icon"
          custom-class-name="primary-svg"
          />
        <!-- <img v-if="item.icon" :src="item.icon" :class="getClasses" /> -->
        <h1 v-if="item.label">{{ item.label }}</h1>
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RadioGroup',
  props: ['items', 'className', 'objectkey', 'customclass', 'defaultValue'],
  computed: {
    getClasses: function () {
      let classes = 'mr-3 '

      if (this.className) {
        classes += this.className
      }
      return classes.trim()
    }
  },
  methods: {
    getRef(item){
      console.log('item', item)
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/revampCV/variables";
[type='radio']:checked,
[type='radio']:not(:checked),
[type='checkbox']:checked,
[type='checkbox']:not(:checked) {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}
.radiogroup-tools:checked + label,
.radiogroup-tools:not(:checked) + label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  font-weight: 400;
  margin: 0 auto;
  border-radius: 5px;
  cursor: pointer;
  color: $primary;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
  box-sizing: border-box;
  backdrop-filter: blur(13px);
}
.radiogroup-tools:not(:checked) + label {
  background-color: #ffffff;
  color: #8E8E8E;
  border: 1px solid #E5E5E5;
  svg.primary-svg {
    rect {
      fill: #DADADA !important;
    }
    circle {
      fill: #DADADA !important;
    }
  }
}
.radiogroup-tools:checked + label {
  background-color: rgba($primary, 0.12);
  border: 1px solid rgba($primary, 0.32);
  color: $primary;
  svg.primary-svg {
    rect {
      fill: rgba($primary, 1) !important;
    }
    circle {
      fill: rgba($primary, 1) !important;
    }
  }
}
</style>
