import store from '@/store'
import {debounce} from './index'

const { body } = document
const WIDTH = 1024 // refer to Bootstrap's responsive design

export default {
  beforeMount() {
    window.addEventListener('resize', debounce(this.$_resizeHandler, 150))
  },
  beforeDestroy() {
    window.removeEventListener('resize', debounce)
  },
  async updated() {
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
    }, 100);
  },
  methods: {
    $isLg() {
      const rect = body.getBoundingClientRect()
      return rect.width < WIDTH
    },
    $scalePreviewCss () {
      let scaleX = 1, scaleY = 1, baseWidth = 794, baseHeight = 1106;
      let pageHeight = document.querySelector('.page-content').clientHeight;
      let pageWidth = document.querySelector('.page-content').clientWidth; 
      scaleX = pageWidth / baseWidth;
      scaleY = pageHeight / baseHeight;

      let newScale = 1;
      let newLeftPos = Math.abs((pageWidth - 794) / 2);
      let newTopPos = 0;

      if (!store.state.fullPreview) {

        newScale = (scaleX > scaleY) ? scaleY : scaleX;
        newLeftPos = Math.abs(Math.floor(((baseWidth * newScale) - pageWidth)/2));
        newTopPos = Math.abs(Math.floor(((baseHeight * newScale) - pageHeight)/2));
      }

      return {
        '-webkit-transform': 'scale(' + newScale + ')',
        left: newLeftPos + 'px',
        top: newTopPos + 'px'
      }
    },
    async $_resizeHandler() {
      if (!document.hidden) {
        await store.commit('updateScreen', this.$isLg())
        if (document.querySelector('.page-content')) {
          await store.commit('updatePreviewCss', this.$scalePreviewCss());
        }
      }
    }
  }
}
