      <div
        v-if="$store.state.user.loggedIn"
        style="border-color: #353d46"
        class="-lg:hidden border-b border-t py-2 mt-6 sidebar-user-action"
        :class="{ 'router-link-active': openSideNav }"
      >
        <div class="border-l-2 border-transparent">
          <div
            @click="openSideNav = !openSideNav"
            class="flex cursor-pointer items-center"
          >
            <img v-if="$store.state.user.user.profileImage"
              :src="$store.state.user.user.profileImage"
              class="w-10 rounded-full"
            />
            <iconly name="profile" type="light" v-else/>
            <div class="w-full flex items-center ml-4" v-show="!menuCollapse">
              <h1 class="text-bule-gray text-sm select-none">{{ $store.state.user.user.name }}</h1>
              <p style="color: #b9b9b9" class="text-xs select-none truncate">
                {{ $store.state.user.user.email }}
              </p>
            </div>
            <iconly name="arrow-down2" type="light"/>
          </div>
        </div>
        <transition name="signindrop">
          <ul
            v-if="openSideNav"
            class="flex flex-col my-2 px-4"
            style="
              transition: transform 0.3s ease-in-out;
              transform-origin: top;
            "
          >
            <router-link
              to="/profile"
              class="
                cursor-pointer
                hover:bg-primary hover:bg-opacity-25
                text-bule-gray
                border-l-2 border-transparent
                px-4
                py-2
                text-sm
              "
              >Profile</router-link
            >
            <router-link
              to="/setting"
              class="
                cursor-pointer
                px-4
                hover:bg-primary hover:bg-opacity-25
                text-bule-gray
                border-l-2 border-transparent
                py-2
                text-sm
              "
              >Account Settings</router-link
            >
            <li
              @click="logout"
              class="
                cursor-pointer
                hover:bg-primary hover:bg-opacity-25
                px-4
                text-bule-gray
                border-l-2 border-transparent
                py-2
                text-sm
              "
            >
              Sign Out
            </li>
          </ul>
        </transition>
      </div>
      <div style="border-color: #353d46" class="lg:mt-6 -lg:flex pb-3 sidebar-menu-list">
        <router-link
          to="/"
          class="
            flex
            -lg:hidden
            -lg:flex-col
            -lg:items-center
            lg:py-2
            hover:bg-active hover:bg-opacity-25
            px-4
            lg:space-x-2
            hover:cursor-pointer
            lg:hover:border-active
            -lg:py-0
            master-link
            rounded
            items-center
          "
        >
          <iconly name="home" type="light" class="w-5 h-5"/>
          <span class="text-sm font-normal">Home</span>
        </router-link>
        <!-- <router-link
          :class="{'activeLink':activeRoute === 'resume'}"
          to="/templates/resume"
          class="
            flex
            -lg:hidden
            -lg:flex-col
            -lg:items-center
            lg:py-2
            hover:bg-active hover:bg-opacity-25
            px-4
            lg:space-x-2
            hover:cursor-pointer
            lg:hover:border-active
            -lg:py-0
            master-link
            rounded
            items-center
          "
        >
          <iconly name="document" type="light" class="w-5 h-5"/>
          <span class="text-sm font-normal">Resume</span>
        </router-link>
        <router-link
          :class="{'activeLink':activeRoute === 'coverletter'}"
          to="/templates/coverletter"
          class="
            flex
            -lg:hidden
            -lg:flex-col
            -lg:items-center
            lg:py-2
            hover:bg-active hover:bg-opacity-25
            px-4
            lg:space-x-2
            hover:cursor-pointer
            lg:hover:border-active
            -lg:py-0
            master-link
            rounded
            items-center
          "
        >
          <iconly name="paper" type="light" class="w-5 h-5"/>
          <span class="text-sm font-normal">Cover Letter</span>
        </router-link> -->
        <router-link
          to="/dashboard"
          class="flex -lg:hidden -lg:flex-col -lg:items-center lg:py-3 hover:bg-active hover:bg-opacity-25 px-4 lg:space-x-4 hover:cursor-pointer lg:hover:border-active -lg:py-0"
          v-if="$store.state.user.loggedIn"
        >
          <img src="/static/img/icon/ico-dashboard.svg" class="w-6" />
          <span
            class="text-sm text-bule-gray"
            >Dashboard</span
          >
        </router-link>
      </div>
    </div>
    <div class="-lg:hidden sidebar-menu-bottom">
      <ul class="flex flex-col space-y-3 text-xs font-normal">
        <li class="flex items-center hover:text-primary cursor-pointer mb-2 px-4" :class="[menuCollapse?'justify-center':'']">
          <router-link
          to="/"
          class="flex items-center"
        >
          <!-- <iconly name="info" type="light"  class="w-5 h-5"/> -->
          <img src="/static/img/icon/info.svg" class="w-5 h-5"/>
          <p class="ml-2 text-sm font-normal">Product Feedback</p>
          </router-link>
        </li>
        <li class="flex items-center hover:text-primary cursor-pointer mb-2 px-4" :class="[menuCollapse?'justify-center':'']">
          <router-link
            to="/"
            class="flex items-center"
          >
            <img src="/static/img/icon/message-circle.svg" class="w-5 h-5"/>
            <!-- <iconly name="morecircle" type="light"  class="w-5 h-5"/> -->
            <p class="ml-2 text-sm font-normal">Support</p>
          </router-link>
        </li>
        <li v-if="!$store.state.user.loggedIn" class="py-2">
          <button class="bg-primary-gradient rounded-md w-full text-white flex text-base justify-center items-center py-2 hover:bg-secondary" @click="show()">
            <iconly name="login" type="light" :class="{'mr-2':!menuCollapse}"/> {{!menuCollapse?'Sign in':''}} 
          </button>
        </li>
      </ul>
    </div>
    <modal
      :width="'750px'"
      :height="'400px'"
      :adaptive="true"
      name="SigninModal"
    >
      <Signin />
    </modal>
    <modal
      :width="'750px'"
      :height="'500px'"
      name="RegisterModal"
    >
      <Signup />
    </modal>
  </div>
</template>

<script>
import Signup from '@/components/Modals/Auth/Signup.vue'
import Signin from '@/components/Modals/Auth/Signin.vue'
import { eventBus } from '@/main'

export default {
  name: 'Sidebar',
  components: {
    Signup,
    Signin,
  },
  watch: {
    '$route': {
      handler: function(route) {
        console.log('route', route)
      },
      deep: true,
      immediate: true
    }
  },
  computed: {
    activeRoute: function() {
      if(this.$route.path.includes('coverletter')){
        return 'coverletter'
      }
      if(this.$route.path.includes('resume')) {
        return 'resume'
      }
      return ''
    },
    routeParts: function () {
      if (
        this.$route.path.includes('workspace') ||
        this.$route.path.includes('check')
      ) {
        return true
      }
      return false
    },
    contentUrl: function () {
      let type = ''
      if (this.$route.path.includes('coverletter')) {
        type = 'coverletter'
      }
      if (this.$route.path.includes('resume')) {
        type = 'resume'
      }
      return '/workspace/' + type + '/content'  
    },
    designUrl: function () {
      let type = ''
      if (this.$route.path.includes('coverletter')) {
        type = 'coverletter'
      }
      if (this.$route.path.includes('resume')) {
        type = 'resume'
      }
      return '/workspace/' + type + '/design'
    },
  },
  methods: {
    show() {
      this.$modal.hide('RegisterModal')
      this.$modal.show('SigninModal')
    },
    logout() {
      this.openSideNav = false
      localStorage.token = ''
      localStorage.selectedresume = ''
      this.$store.commit('user/makeLoggedOut')
      eventBus.$emit('initialize')
      if (this.$route.path == '/dashboard'){
        this.$router.push('/')
      }
    },
    menuClass: function (menu) {
      let activeBorderClass = false
      let tranBorderClass = true

      if (this.$route.path.includes(menu)) {
        activeBorderClass = true
        tranBorderClass = false
      }

      return {
        'active:border-active': activeBorderClass,
        'lg:border-transparent': tranBorderClass
      }
    }
  },
  props: ['menuCollapse'],
  data() {
    return {
      openSideNav: false,
      sideBar: true,
      selectedMenu: ''
    }
  }
}
</script>
