<template>
    <div>
        <h1 class="text-lg text-primary-3 font-medium">
            {{ data.category }}
        </h1>
        <p class="mt-1 text-primary-2">
            {{ skills.join(', ') }}
        </p>
    </div>
</template>

<script>
export default {
  props: ['data'],
  computed: {
    skills () {
      let skills = []

      for (const skill of this.data.skills) {
        skills.push(skill.name)
      }

      return skills
    }
  }
}
</script>