import Vue from 'vue'
import Vuex from 'vuex'
import main from './modules/main'
import cldesign from './modules/cldesign'
import clcontent from './modules/clcontent'
import rcontent from './modules/rcontent'
import user from './modules/user'
import resumes from './modules/resumes'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    main,
    cldesign,
    clcontent,
    rcontent,
    user,
    resumes
  },
  state: {
    mobileScreen: false,
    previewCss: {},
    education: [],
    profile: [],
    totalPreviewPages: 1,
    fullPreview: false,
    fullScreenProgress: null
  },
  getters: {
    getScreen: state => {
      return state.mobileScreen
    },
  },
  mutations: {
    updateScreen(state, data) {
      state.mobileScreen = data
    },
    updatePreviewCss(state, data) {
      state.previewCss = data
    },
    
    updateTotalPreviewPages(state, data) {
      state.totalPreviewPages = data
    },
    fullPreviewMode (state, data) {
      state.fullPreview = data
    },
  }
})
