<template>
  <div :class="customclass">
    <div v-for="item in items" :key="item.theme_id" 
        class="col-span-1 relative radius-8" 
        :style="{width:'40px', height: '40px',background: createBackgroundString(item) }">
      <input
        class="radio-tools"
        type="radio"
        name="themeColor"
        :id="item.theme_id"
        @click="getSelectedTheme(item)"
        :checked="item.theme_id == defaultValue"
      />
      <label class="for-radio-tools cursor-pointer radius-8 border-2" :for="item.theme_id" :style="{ backgroundImage: createBackgroundString(item) }"
        >
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RadioThemeColor',
  props: ['items', 'className', 'customclass', 'defaultValue'],
  computed: {
  },
  methods: {
        createBackgroundString(item) {
            return `linear-gradient(to right bottom, ${item.bg_color} 50%, ${item.text_color} 50%)`
        },
        getSelectedTheme(item) {
          return item;
            // console.log('theme', item);
        }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
[type='radio']:checked,
[type='radio']:not(:checked) {
  visibility: hidden;
}
.radio-tools + label {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 0;
}
.radio-tools:checked + label {
  border: 2px solid $primary;
  color: $primary;
}
</style>
