    </div>
    <div class="flex justify-between items-center border-t relative z-10">
      <h1 class="p-6">{{ name }}</h1>
      <div
        @click="editbutton = !editbutton"
        class="flex items-center justify-center cursor-pointer p-6"
      >
        <img src="/static/img/icon/heroicons/dots-horizontal.svg" class="cursor-pointer w-6" />
