              <img src="/static/img/icon/heroicons/pencil.svg" class="w-5" />
              <span class="hover:text-button">Edit</span>
            </div>
            <li class="flex items-center space-x-4 cursor-pointer">
              <img src="/static/img/icon/heroicons/download.svg" class="w-5" />
