            </li>
            <li class="flex items-center space-x-4 cursor-pointer">
              <img src="/static/img/icon/heroicons/document-download.svg" class="w-5" />
              <span class="hover:text-button">Export to Word</span>
            </li>
            <li class="flex items-center space-x-4 cursor-pointer">
              <img src="/static/img/icon/heroicons/mail.svg" class="w-5" />
              <span class="hover:text-button">Send to Email</span>
            </li>
            <li class="flex items-center space-x-4 cursor-pointer">
              <img src="/static/img/icon/heroicons/document-duplicate.svg" class="w-5" />
