              <img src="/static/img/icon/heroicons/trash.svg" class="w-5" />
              <span class="hover:text-button">Delete</span>
            </li>
            <li class="flex items-center space-x-4 cursor-pointer">
              <img src="/static/img/icon/heroicons/document-search.svg" class="w-5" />
              <span class="hover:text-button">Analyze</span>
            </li>
            <li class="flex items-center space-x-4 cursor-pointer">
              <img src="/static/img/icons-primary/professional.svg" class="w-5" />
              <span class="hover:text-button">Hire a professional</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
