</template>

<script>
export default {
  props: ['icon', 'type', 'name', 'idx'],
  data() {
    return {
      editbutton: false
    }
  },
  methods: {
    redirectUser: function (route) {
      localStorage.selectedresume = this.idx
      this.$router.push(route)
    }
  }
}
</script>
