<template>
  <div :class="customclass">
    <div>
      <input
        class="checkbox-tools"
        type="checkbox"
        :id="objectkey + '-' + truevalue"
        @change="
          $store.commit('cldesign/setDesignByKey', {
            key: objectkey,
            value: toggle
          })
        "
        v-model="toggle"
        :true-value="truevalue"
        :false-value="falsevalue"
      />
      <label class="for-checkbox-tools" :for="objectkey + '-' + truevalue">
        <img v-if="icon" :src="icon" :class="getClasses" />
        <h1 v-if="label">{{ label }}</h1>
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CheckboxNew',
  props: [
    'label',
    'icon',
    'className',
    'objectkey',
    'customclass',
    'truevalue',
    'falsevalue',
    'defaultvalue'
  ],
  data: function () {
    return {
      toggle: this.defaultvalue
    }
  },
  watch: {
    defaultvalue: function (val) {
      this.toggle = val
    }
  },
  computed: {
    getClasses: function () {
      let classes = 'mr-3 '

      if (this.className) {
        classes += this.className
      }
      return classes.trim()
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
[type='checkbox']:checked,
[type='checkbox']:not(:checked) {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}
.checkbox-tools:checked + label,
.checkbox-tools:not(:checked) + label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  font-weight: 400;
  margin: 0 auto;
  border-radius: 5px;
  cursor: pointer;
  color: $primary;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
.checkbox-tools:not(:checked) + label {
  background-color: #ffffff;
  color: #686c70;
  border: 1px solid #E5E5E5;
}
.checkbox-tools:checked + label {
  background-color: rgba($primary, 0.12);
  border: 1px solid rgba($primary, 0.32);
  color: $primary;
}
</style>
