<template>
  <div :style="divStyle" id="divpdf">
    <div :style="pageStyle" id="pdfpreview">
      <div :style="headerStyle">
        <div>
          <p :style="nameStyle">
            <span style="font-size: 30px;">{{ firstFirstName }}</span>{{ restFirstName }} 
            <span style="font-size: 30px;">{{ firstLastName }}</span>{{ restLastName }}
          </p>
          <p style="font-size: 18px; font-weight: 700;">
            {{ this.$store.state.rcontent.content.personal.jobtitle}} 
          </p>
        </div>
      </div>
      <div>
        <ul 
        v-if="ulComputed"
        style="text-align: right;list-style: none;padding: 6px 0;">
          <li
          v-if="addressComputed" 
          style="font-size: 15px;color: #848484;margin: 4px 0;">
            <em>
              {{ this.$store.state.rcontent.content.personal.state }}
              {{ this.$store.state.rcontent.content.personal.state && 
                ( this.$store.state.rcontent.content.personal.city 
                || this.$store.state.rcontent.content.personal.zipcode) ? ', ' : '' }}
              {{ this.$store.state.rcontent.content.personal.city }} 
              {{ this.$store.state.rcontent.content.personal.zipcode }}
            </em>
          </li>
          <li 
            v-if="this.$store.state.rcontent.content.personal.email || 
                this.$store.state.rcontent.content.personal.phone"
            style="font-size: 15px;color: #848484;margin: 4px 0;">
            <em>
              {{ this.$store.state.rcontent.content.personal.phone }} 
              {{ this.$store.state.rcontent.content.personal.email && 
                this.$store.state.rcontent.content.personal.phone ? '| ' : '' }} 
              {{ this.$store.state.rcontent.content.personal.email }}
            </em>
          </li>
        </ul>
      </div>
      <profile 
        :content="$store.state.rcontent.content.profile"
        :bodyStyle="bodyStyle"
        @onUpdate="updatePageBreak"
      />
      <template
        v-for="content in $store.state.rcontent.content.addedContents"
      >
        <component 
          :key="content.id" 
          :is="content.id" 
          :content="content"
          :sectionTitleStyle="sectionTitleStyle"
          :bodyStyle="bodyStyle"
          :titleAliasChunks="titleAliasChunks(content.titleAlias)"
          @onUpdate="updatePageBreak"
        />
      </template>
    </div>
  </div>
</template>

<script>
import { debounce, pagebreak, empty } from '@/utils'
import sections from '@/template/resume/resume3section'

export default {
  components: {
    ...sections
  },
  data () {
    return {
      divStyle: {
        width: '794px',
        background: 'white',
        display: 'block',
        boxShadow: '0 0 0.5cm rgb(0 0 0 / 50%)'
      },
      pageStyle: {
        fontFamily: "'Roboto', sans-serif",
        lineHeight: '1.15',
        boxSizing: 'border-box',
        paddingLeft: '60px',
        paddingRight: '60px',
      },
      headerStyle: {
        paddingTop: '50px',
        borderBottom: '3px solid #368595',
        paddingBottom: '2px',
      },
      bodyStyle: {
        paddingTop: '10px',
        fontSize: '15px',
        fontWeight: 400,
        color: '#333',
      },
      nameStyle: {
        fontSize: '22px',
        color: '#368595',
        fontWeight: 900,
        letterSpacing: '2px',
        textTransform: 'uppercase',
      },
      sectionTitleStyle: {
        paddingTop: '10px',
        fontSize: '17px',
        letterSpacing: '2px',
        fontWeight: 900,
        color: '#368595',
        textTransform: 'uppercase',
        borderTop: '3px solid #CAC9CB',
      },
      height: 1106,
    }
  },
  methods: {
    empty,
    updatePageBreak: function () {
      debounce(
        pagebreak(
          this.height,
          37,
          40
        ),
        150
      )
    },
    titleAliasChunks (title) {
      let finalChunks = [];
      let chunks = title.split(" ");

      for (const chunk of chunks) {
        finalChunks.push({
          first: chunk.charAt(0),
          rest: chunk.substring(1),
        })
      }

      return finalChunks;
    },
  },
  computed: {
    ulComputed() {
      return (
        this.$store.state.rcontent.content.personal.state +
        this.$store.state.rcontent.content.personal.city +
        this.$store.state.rcontent.content.personal.zipcode +
        this.$store.state.rcontent.content.personal.email +
        this.$store.state.rcontent.content.personal.phone
      )
    },
    addressComputed() {
      return (
        this.$store.state.rcontent.content.personal.state +
        this.$store.state.rcontent.content.personal.city +
        this.$store.state.rcontent.content.personal.zipcode
      )
    },
    firstFirstName() {
      return this.$store.state.rcontent.content.personal.firstname.charAt(0);
    },
    firstLastName() {
      return this.$store.state.rcontent.content.personal.lastname.charAt(0);
    },
    restFirstName() {
      return this.$store.state.rcontent.content.personal.firstname.substring(1);
    },
    restLastName() {
      return this.$store.state.rcontent.content.personal.lastname.substring(1);
    }
  },
  updated: function () {
    this.updatePageBreak()
  },
  mounted: function () {
    this.updatePageBreak()
  },
}
</script>