<template>
  <div :style="divStyle" id="divpdf">
    <div :style="pageStyleComputed" id="pdfpreview">
      <div id="header-wrapper" :style="headerWrapperComputed">
        <div :style="headerStyleComputed">
          <header v-if="personalDetail.firstName">
            <p>
              <span :style="nameStyleComputed">
                {{ personalDetail.firstName }}
                {{ personalDetail.lastName }}</span
              >
              <span :style="dateComputed">{{ letterContent.date }}</span>
            </p>

            <p :style="subtitleStyleComputed">
              <svg
                v-if="
                  this.$store.state.cldesign.design.layout.header.icon ==
                    'icon' && addressCompunted
                "
                class="w-5 h-5 inline-block"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
                ></path>
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
                ></path>
              </svg>
              <svg
                v-if="
                  this.$store.state.cldesign.design.layout.header.icon ==
                    'bullet' && addressCompunted
                "
                class="w-5 h-5 inline-block"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M21,12c0,5-4,9-9,9s-9-4-9-9s4-9,9-9S21,7,21,12z"
                />

                <path
                  fill="currentColor"
                  d="M12,9c1.7,0,3,1.3,3,3s-1.3,3-3,3s-3-1.3-3-3S10.3,9,12,9z"
                />
              </svg>

              {{ personalDetail.address }}{{ personalDetail.country ? ',' : ''
              }}{{ personalDetail.country }}
            </p>

            <p :style="subtitleStyleComputed">
              <svg
                v-if="
                  this.$store.state.cldesign.design.layout.header.icon ==
                    'icon' && personalDetail.phoneNumber
                "
                class="w-5 h-5 inline-block"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z"
                ></path>
              </svg>
              <svg
                v-if="
                  this.$store.state.cldesign.design.layout.header.icon ==
                    'bullet' && personalDetail.phoneNumber
                "
                class="w-5 h-5 inline-block"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M21,12c0,5-4,9-9,9s-9-4-9-9s4-9,9-9S21,7,21,12z"
                />
                <path
                  fill="currentColor"
                  d="M12,9c1.7,0,3,1.3,3,3s-1.3,3-3,3s-3-1.3-3-3S10.3,9,12,9z"
                />
              </svg>

              {{ personalDetail.phoneNumber ? 'T : ' : '' }}
              {{ personalDetail.phoneNumber }}

              {{
                this.$store.state.cldesign.design.layout.header.icon == ',' &&
                personalDetail.phoneNumber
                  ? '-'
                  : ''
              }}

              <svg
                v-if="
                  this.$store.state.cldesign.design.layout.header.icon ==
                    'icon' && personalDetail.email
                "
                class="w-5 h-5 inline-block"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"
                ></path>
              </svg>
              <svg
                v-if="
                  this.$store.state.cldesign.design.layout.header.icon ==
                    'bullet' && personalDetail.email
                "
                class="w-5 h-5 inline-block"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M21,12c0,5-4,9-9,9s-9-4-9-9s4-9,9-9S21,7,21,12z"
                />
                <path
                  fill="currentColor"
                  d="M12,9c1.7,0,3,1.3,3,3s-1.3,3-3,3s-3-1.3-3-3S10.3,9,12,9z"
                />
              </svg>
              {{ personalDetail.email ? 'E :' : '' }} {{ personalDetail.email }}
            </p>
          </header>
        </div>
      </div>

      <div class="body-wrapper">
        <div id="body-inner" :style="bodyWrapperComputed">
          <div :style="paragraphStyleComputed">
            <p>
              {{ employerDetail.title ? 'To : ' : '' }}

              {{ employerDetail.title }}

              {{ employerDetail.firstName }}

              {{ employerDetail.lastName }}
              {{ employerDetail.firstName ? ',' : '' }}

              {{ employerDetail.role }}
              {{ employerDetail.role ? ',' : '' }}

              {{ employerDetail.companyAddress.street }}

              {{
                employerDetail.companyAddress.street &&
                employerDetail.companyAddress.city
                  ? ','
                  : ''
              }}

              {{ employerDetail.companyAddress.city }}

              {{
                employerDetail.companyAddress.city &&
                employerDetail.companyAddress.country
                  ? ','
                  : ''
              }}

              {{ employerDetail.companyAddress.country }}

              {{ employerDetail.phoneNumber ? '|' : '' }}
              {{ employerDetail.phoneNumber }}
              {{
                employerDetail.email && employerDetail.phoneNumber ? '|' : ''
              }}
              {{ employerDetail.email }}
            </p>
            <div
              style="
                width: 100%;
                border: solid 1px black;
                margin: auto;
                margin-top: 2px;
              "
              v-if="employerDetail.title"
            ></div>
          </div>
          <br />

          <p :style="titleStyleComputed">
            {{ letterContent.subject }}
          </p>
          <br />
          <p>{{ letterContent.salutation }}</p>
          <br />

          <div
            :style="paragraphStyleComputed"
            v-html="letterContent.description"
          ></div>
          <div :style="footerStyleComputed">
            <div>
              <p v-if="!empty(letterContent.description)">
                {{ letterContent.closing }}
                {{ letterContent.closing ? ',' : '' }}
              </p>
              <p style="padding-top: 16px">
                {{ personalDetail.fullName }}
              </p>
            </div>
          </div>
        </div>
        <br />
      </div>
    </div>
  </div>
</template>
<script>
import { get } from 'lodash'
import { empty } from '@/utils'
import { debounce, pagebreak } from './../../utils'

export default {
  props: {
    content: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      divStyle: {
        width: '794px',
        background: 'white',
        display: 'block',
        boxShadow: '0 0 0.5cm rgb(0 0 0 / 50%)'
      },
      pageStyle: {
        fontFamily: "'Open Sans', sans-serif"
      },
      headerStyle: {
        paddingBottom: '16px',
        minHeight: '100px',
        width: '85%',
        margin: 'auto',
        paddingTop: '20px'
      },
      nameStyle: {},
      titleStyle: {},
      subtitleStyle: {},
      employerStyle: {
        textAlign: 'left',
        padding: '16px 0px',
        lineheight: '1.5px'
      },
      footerStyle: {
        textAlign: 'left',
        paddingTop: '35px'
      },
      paragraphStyle: {
        width: '100%',
        height: 'auto',
        padding: '0px'
      },
      headerWrapper: {
        minHeight: '150px',
        paddingTop: '20px'
      },
      bodyWrapper: {
        width: '85%',
        margin: 'auto'
      },
      dateWrapper: {
        position: 'absolute',
        fontSize: '10pt',
        fontWeight: 400
      },
      height: 1106,
      pixel: 'pt'
    }
  },

  computed: {
    personalDetail() {
      return get(this.content, 'personalDetail', {})
    },
    letterContent() {
      return get(this.content, 'letterContent', {})
    },
    employerDetail() {
      return get(this.content, 'employerDetail', {})
    },
    companyAddressInfo() {
      return get(this.content, 'employerDetail.companyAddress', {})
    },
    pageStyleComputed() {
      return {
        ...this.pageStyle,
        backgroundColor:
          this.$store.state.cldesign.design.background.body.color || '#FFFFFF'
      }
    },
    employerStyleComputed() {
      return {
        ...this.employerStyle,
        paddingLeft:
          this.$store.state.cldesign.design.page.margin.side + this.pixel,
        paddingRight:
          this.$store.state.cldesign.design.page.margin.side + this.pixel
      }
    },
    nameStyleComputed() {
      return {
        ...this.nameStyle,
        fontFamily: this.$store.state.cldesign.design.font.name.fontfamily,
        fontSize:
          this.$store.state.cldesign.design.font.name.size + this.pixel ||
          '18pt',
        fontWeight: this.$store.state.cldesign.design.font.name.weight,
        textTransform: this.$store.state.cldesign.design.font.name
          .texttransform,
        fontStyle: this.$store.state.cldesign.design.font.name.fontstyle,
        color: this.$store.state.cldesign.design.font.name.color || '#000000',
        letterSpacing: '0px',
        textAlign: this.$store.state.cldesign.design.font.name.textalign
      }
    },
    titleStyleComputed() {
      return {
        ...this.titleStyle,
        fontFamily:
          this.$store.state.cldesign.design.font.title.fontfamily ||
          this.$store.state.cldesign.design.font.paragraph.fontfamily,
        fontSize:
          this.$store.state.cldesign.design.font.title.size + this.pixel ||
          this.$store.state.cldesign.design.font.paragraph.size ||
          '12pt',
        fontWeight:
          this.$store.state.cldesign.design.font.title.weight ||
          this.$store.state.cldesign.design.font.paragraph.weight,
        textTransform:
          this.$store.state.cldesign.design.font.title.texttransform ||
          this.$store.state.cldesign.design.font.paragraph.texttransform,
        fontStyle:
          this.$store.state.cldesign.design.font.title.fontstyle ||
          this.$store.state.cldesign.design.font.paragraph.fontstyle,
        color:
          this.$store.state.cldesign.design.font.title.color ||
          this.$store.state.cldesign.design.font.paragraph.color ||
          '#000000',
        marginTop: '4px'
      }
    },
    subjectStyleComputed() {
      return {
        ...this.titleStyle,
        fontFamily:
          this.$store.state.cldesign.design.font.title.fontfamily ||
          this.$store.state.cldesign.design.font.paragraph.fontfamily,
        fontSize:
          this.$store.state.cldesign.design.font.title.size + this.pixel ||
          this.$store.state.cldesign.design.font.paragraph.size ||
          '12pt',
        fontWeight:
          this.$store.state.cldesign.design.font.title.weight ||
          this.$store.state.cldesign.design.font.paragraph.weight,
        textTransform:
          this.$store.state.cldesign.design.font.title.texttransform ||
          this.$store.state.cldesign.design.font.paragraph.texttransform,
        fontStyle:
          this.$store.state.cldesign.design.font.title.fontstyle ||
          this.$store.state.cldesign.design.font.paragraph.fontstyle,
        color:
          this.$store.state.cldesign.design.font.title.color ||
          this.$store.state.cldesign.design.font.paragraph.color ||
          '#000000',
        marginTop: '4px',
        textAlign: 'center',
        width: '90%'
      }
    },
    subtitleStyleComputed() {
      return {
        ...this.subtitleStyle,
        fontFamily: this.$store.state.cldesign.design.font.subtitle.fontfamily,
        fontSize:
          this.$store.state.cldesign.design.font.subtitle.size + this.pixel ||
          '12pt',
        fontWeight: this.$store.state.cldesign.design.font.subtitle.weight,
        textTransform: this.$store.state.cldesign.design.font.subtitle
          .texttransform,
        fontStyle: this.$store.state.cldesign.design.font.subtitle.fontstyle,
        color:
          this.$store.state.cldesign.design.font.subtitle.color || '#000000'
      }
    },
    paragraphStyleComputed() {
      return {
        ...this.paragraphStyle,
        fontFamily: this.$store.state.cldesign.design.font.paragraph.fontfamily,
        fontSize:
          this.$store.state.cldesign.design.font.paragraph.size + this.pixel ||
          '12pt',
        fontWeight: this.$store.state.cldesign.design.font.paragraph.weight,
        textTransform: this.$store.state.cldesign.design.font.paragraph
          .texttransform,
        fontStyle: this.$store.state.cldesign.design.font.paragraph.fontstyle,
        textAlign:
          this.$store.state.cldesign.design.layout.body.textalign || 'left',
        textIndent:
          this.$store.state.cldesign.design.layout.body.textindent + this.pixel,
        letterSpacing:
          this.$store.state.cldesign.design.layout.body.letterspacing +
          this.pixel,
        wordSpacing:
          this.$store.state.cldesign.design.layout.body.wordspacing +
          this.pixel,
        lineHeight: this.$store.state.cldesign.design.layout.body.lineheight,
        backgroundColor:
          this.$store.state.cldesign.design.background.body.color || '#FFFFFF',
        color:
          this.$store.state.cldesign.design.font.paragraph.color || '#000000'
      }
    },
    dateComputed() {
      if (this.$store.state.cldesign.design.layout.header.textalign == 'left') {
        return {
          ...this.dateWrapper,
          textAlign: 'left',
          right: '50px',
          top: '90px'
        }
      } else if (
        this.$store.state.cldesign.design.layout.header.textalign == 'right'
      ) {
        return {
          ...this.dateWrapper,
          textAlign: 'right',
          right: '650px',
          top: '90px'
        }
      } else {
        return {}
      }
    },
    headerStyleComputed() {
      return {
        ...this.headerStyle,
        textAlign:
          this.$store.state.cldesign.design.layout.header.textalign || 'left',
        backgroundColor:
          this.$store.state.cldesign.design.background.header.color ||
          '#FFFFFF',
        color:
          this.$store.state.cldesign.design.background.header.text || '#000000',
        paddingTop: '50px'
      }
    },
    footerStyleComputed() {
      return {
        ...this.footerStyle,
        paddingBottom: this.$store.state.cldesign.design.page.layout.tbpadding,
        fontFamily: this.$store.state.cldesign.design.font.paragraph.fontfamily
      }
    },
    addressCompunted() {
      return (
        this.personalDetail.street +
        this.personalDetail.state +
        this.personalDetail.city +
        this.personalDetail.country +
        this.personalDetail.zipcode
      )
    },
    phoneemailComputed() {
      return this.personalDetail.phoneNumber && this.personalDetail.email
    },
    headerWrapperComputed() {
      return {
        ...this.headerWrapper,
        backgroundColor:
          this.$store.state.cldesign.design.background.header.color || '#FFFFFF'
      }
    },
    bodyWrapperComputed() {
      return {
        ...this.bodyWrapper,
        backgroundColor:
          this.$store.state.cldesign.design.background.body.color || '#FFFFFF'
      }
    }
  },
  methods: {
    empty,
    updatePageBreak: function () {
      debounce(
        pagebreak(
          this.height,
          37,
          parseInt(this.$store.state.cldesign.design.page.layout.lrpadding) + 96
        ),
        150
      )
    }
  },
  updated: function () {
    this.updatePageBreak()
  },
  mounted: function () {
    this.updatePageBreak()
  },
  created: function () {
    this.$store.commit('cldesign/setDefaultSetting')
  }
}
</script>



