<template>
  <select
    :value="value"
    @input="$emit('input', $event.target.value)"
    :class="[
      value == 0 ? 'text-gray-500' : '',
      white ? 'bg-white border-white focus:border-none' : ' border border-gray-400 '
    ]"
    class="form-select w-full py-3 px-3 pr-8 text-gray-500 border border-gray-400 rounded focus:outline-none text-sm focus:bg-white focus:border-teal-600"
  >
    <option v-if="placeholder" :value="null" disabled class="text-gray-500" selected="selected">
      {{ placeholder }}
    </option>
    <option 
      class="text-black"
      v-for="country in countries"
      :key="country.name"
      :value="country.name"
    >
      {{ country.name }}
    </option>
  </select>

</template>

<script>
import countries from '@/utils/countries'

export default {
  name: 'CountrySelect',
  props: ['placeholder', 'white', 'value'],
  data () {
    return {
      countries: []
    }
  },
  created() {
    this.countries = countries
  },
}
</script>

<style scoped>
  select:focus {
    box-shadow: none !important;
  }
</style>
