
const state = () => ({
  content: null,
  defaultcontent: {
    personalDetail: {
      firstName: '',
      lastName: '',
      fullName: '',
      phoneNumber: '',
      street: '',
      city: '',
      state: '',
      country: '',
      zipcode: '',
      email: '',
    },
    employerDetail: {
      firstName: '',
      lastName: '',
      title: '',
      role: '',
      department: '',
      street: '',
      city: '',
      state: '',
      country: '',
      zipcode: '',
      company: '',
      companyAddress: {
        street: '',
        city: '',
        state: '',
        country: '',
        zipcode: '',
        phoneNumber: '',
        email: ''
      }
    },
    letterContent: {
      subject: '',
      description: '',
      date: '',
      closing: ''
    }
  }
})

const mutations = {
  resetCoverletterContent (state) {
    state.content = JSON.parse(JSON.stringify(state.defaultcontent));
  },
  updateCoverLetterOnFly(state, data) {
    state.content = data
  },
}

export default {
  namespaced: true,
  state,
  mutations,
}