<template>
  <div
    class="h-full w-full flex items-center flex-col p-5"
  >
    <div class="flex justify-center">
      <h1
        class="font-semibold text-gray-800 text-2xl border-button"
      >
        Get started with your account
      </h1>
      <img
        src="/static/img/icon/heroicons/close.svg"
        @click="hideRegister"
        class="w-5 h-5 cursor-pointer absolute top-0 right-0 mt-5 mr-5"
      />
    </div>
    <p @click="show" class="text-primary-1">
      Already have an account?
      <span @click="show" class="text-button cursor-pointer"
        >Sign In</span
      >
    </p>
    <div v-show="errMsg" class="w-full mt-5 bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative" role="alert">
      <span class="block sm:inline">{{ errMsg }}.</span>
      <span class="absolute top-0 bottom-0 right-0 px-4 py-3">
        <img
          @click="errMsg = ''" 
          class="fill-current h-5 w-5 text-red-500 cursor-pointer"
          src="/static/img/icon/heroicons/close-red.svg"
        />
      </span>
    </div>
    <div class="flex justify-between items-center w-full mt-10">
      <div style="width: 45%" class="flex flex-col gap-5">
        <Input v-model.trim.lazy="form.name" placeholder="Name" :error="validation.firstError('form.name')" />
        <Input v-model.trim.lazy="form.email" placeholder="Email" :error="validation.firstError('form.email')" />
        <div>
          <input
            type="password"
            placeholder="Password"
            v-model.trim.lazy="form.password"
            class="w-full py-3 px-3 text-sm text-gray-700  border border-gray-400 rounded focus:outline-none focus:bg-white focus:border-2 focus:border-teal-600"
            :class="{ 'border-danger': validation.firstError('form.password') }"
            />
          <span class="text-danger text-sm" v-show="validation.firstError('form.password')">{{ validation.firstError('form.password') }}</span>
        </div>
        <label class="flex items-start text-primary-2">
          <input
            type="checkbox"
            class="form-checkbox focus:outline-none text-button focus:shadow-none mt-1"
          />
          <span class="ml-2 text-xs"
            >By Registering, you agree that you've accepted our User
            Agreement, and you consent to our Privacy Notice.</span
          >
        </label>
        <Button @click.native="register" class="py-3" value="Sign Up with Email" />
      </div>
      <div
        style="width: 6%"
        class="flex flex-col items-center justify-center"
      >
        <div style="width: 1px" class="h-10vh bg-primary-3" />
        <p class="text-primary-1">or</p>
        <div style="width: 1px" class="h-10vh bg-primary-3" />
      </div>
      <div style="width: 45%" class="flex flex-col gap-5">
        <button
          class="bg-white shadow-lg rounded font-Poppins w-full text-primary-1 flex items-center focus:outline-none px-6 py-3 border-2 border-transparent"
        >
          <img src="/static/img/icon/google.png" class="w-5 mr-3" />
          <h1>Sign Up With Google</h1>
        </button>
        <button
          style="background: #022f89"
          class="rounded w-full font-Poppins text-white flex items-center focus:outline-none px-6 py-3 border-2 border-transparent"
        >
          <img src="/static/img/icon/facebook.png" class="w-5 mr-3" />
          <h1>Sign Up Facebook</h1>
        </button>
        <button
          style="background: #007ab6"
          class="rounded w-full font-Poppins text-white flex items-center focus:outline-none px-6 py-3 border-2 border-transparent"
        >
          <img src="/static/img/icon/linkedin.png" class="w-5 mr-3" />
          <h1>Sign Up LinkedIn</h1>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import Input from '@/components/Ui/Input.vue'
import Button from '@/components/Ui/Button.vue'
import { eventBus } from '@/main'
import SimpleVueValidation from 'simple-vue-validator';
const Validator = SimpleVueValidation.Validator;

export default {
  components: {
    Input,
    Button,
  },
  data: function () {
    return {
      form: {
        name: null,
        email: '',
        password: '',
      },
      errMsg: '',
    }
  },
  validators: {
    'form.name': function (value) {
      return Validator.value(value).required();
    },
    'form.email': function (value) {
      return Validator.value(value).required().email();
    },
    'form.password': function (value) {
      return Validator.value(value).required();
    },
  },
  methods: {
    hideRegister() {
      this.$modal.hide('RegisterModal')
    },
    show() {
      this.$modal.hide('RegisterModal')
      this.$modal.show('SigninModal')
    },
    register () {
      this.$validate()
      .then((success) => {
        if (success) {
          this.$store.state.fullScreenProgress = this.$loading.show()
          this.$store.dispatch('user/signup', this.form)
          .then((response) => {
            if (response.status == 500 || response.status == 400) {
              this.errMsg = response.message
            }
            if (response.code) {
              localStorage.token = response.message;
            }
            this.$store.state.fullScreenProgress.hide()
            if (response.code) {
              this.hideRegister()
              eventBus.$emit('authenticated', '/')
            }
          })
        }
      });
    }
  }
}
</script>