<template>
  <div class="">
    <div class="grid grid-cols-3 space-y-4">
      <div class="col-span-1">
        <div class="bg-white rounded-b">
          <div class="flex items-center justify-between">
            <div class="flex flex-col items-center space-y-4">
              <div :class="avatarClass">
                <img
                  :src="avatarUrl"
                  class="w-full object-cover"
                />
              </div>
              <div class="">
                <button class="no-filled button-primary rounded-full w-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
                  @click="avatarClick">
                  {{ editAvatar ? 'Edit' : 'Upload' }} Photo 
                  <input type="file" ref="avatar" name="avatar" style="display: none" @change="previewFile" />
                </button>
                <!-- <button
                  class="w-32 gap-2 py-3 text-primary-1 rounded flex justify-center items-center border-gray-500 focus:outline-none border-2"
                  @click="avatarClick"
                >
                  <img class="h-6 w-6" :src="'/static/img/icon/heroicons/' + (editAvatar ? 'pencil-alt' : 'plus') + '-535861.svg'" />
                  {{ editAvatar ? 'Edit' : 'Upload' }} Photo 
                  <input type="file" ref="avatar" name="avatar" style="display: none" @change="previewFile" />
                </button> -->
              </div>
              <MySwitch label="Hide Photo"  v-model="form.isPhotoHide"/>
            </div>
          </div>
        </div>
      </div>
      <div class="col-span-2 space-y-5">
        <div class="flex items-center gap-5">
          <div class="flex-1">
            <Input placeholder="Enter Title"  v-model="form.title"/>
          </div>
          <div class="flex-1">
            <Input placeholder="First Name" v-model="form.firstname" />
          </div>
          <div class="flex-1">
            <Input placeholder="Last Name" v-model="form.lastname" />
          </div>
        </div>
        <div>
          <Input placeholder="Job title" v-model="form.jobtitle"/>
        </div>
        <div class="grid grid-cols-3 items-center gap-5">
          <div class="col-span-2">
            <Input placeholder="Email" v-model="form.email" />
          </div>
          <div class="col-span-1">
            <Input placeholder="Phone" v-model="form.phone"/>
          </div>
        </div>
        <div class="grid grid-cols-3 items-center gap-5">
          <div class="col-span-2">
          <Input placeholder="Street" v-model="form.street"/>
          </div>
          <div class="col-span-1">
            <CountrySelect v-model="form.country" placeholder="Country" />
          </div>
        </div>
        <div class="grid grid-cols-3 gap-x-5">
          <div>
            <Input placeholder="City" v-model="form.city"/>
          </div>
          <div>
            <Input placeholder="State" v-model="form.state"/>
          </div>
          <div>
            <Input placeholder="Zip Code" v-model="form.zipcode" />
          </div>
        </div>
        <div class="grid grid-cols-12 gap-x-5" 
          v-for="(field, index) in form.addedSocialFields"
          :key="field.key"
        >
          <h1 class=" col-span-2 flex items-center font-medium text-primary-1 text-sm">
            {{ field.key }}
          </h1>
          <Input class="col-span-8" v-model="form.addedSocialFields[index].value" />
          <button 
              class="col-span-2 rounded flex text-danger m-1 justify-center items-center focus:outline-none p-1 border-2 border-danger"
              @click="removeSocialField(index)"
            >
            X
            </button>
        </div>
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Additional Fields 
          </h1>
          <div class="flex flex-wrap">
            <button 
              class="rounded flex text-primary-1 mr-2 mb-2 justify-center items-center focus:outline-none p-2 border-2"
              v-for="(field, index) in form.socialFields"
              :key="field"
              @click="addSocialField(index)"
            >
              <img src="/static/img/icon/heroicons/plus-gray.svg?v=3" class="w-6">
              {{ field }}
            </button>
          </div>
        </div>
      </div>

      <!-- <div class="flex items-center justify-end space-x-5">
        <button
          @click="onCancel"
          class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
        >
          Cancel
        </button>
        <Button @click.native="onSubmit" value="Save" />
      </div> -->
      <div class="flex space-x-4 items-center absolute content-action-button">
        <button class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
          @click="onCancel">
            Cancel
        </button>
        <button class="bg-primary border border-primary text-white rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
          @click.native="onSubmit">
            Save
        </button>
      </div>
    </div>
    <modal id="avatar-modal" height="auto" name="AvatarModal">
      <cropper
        :src="form.originalAvatar"
        :stencil-component="form.stencil"
        ref="cropper"
      />
      <div class="flex justify-between p-8">
        <div class="grid grid-cols-2 gap-x-5">
          <button 
            @click="changeStencil('rectangle-stencil')"
            class="bg-white rounded border py-2 border-edit flex items-center justify-center px-4">
            <img src="/static/img/icon/heroicons/square.svg" class="w-6 h-6">
          </button>
          <button 
            @click="changeStencil('circle-stencil')"
            class="bg-white rounded border py-2 border-edit flex items-center justify-center px-4">
            <img src="/static/img/icon/heroicons/circle.svg" class="w-6 h-6">
          </button>
        </div>
        <div class="grid grid-cols-2 gap-x-5">
          <button
            class="bg-main-button rounded bg-opacity-10 text-button flex justify-center items-center focus:outline-none py-2 px-4 border-button"
            style="border-width: 1px;"
            @click="saveImage"
          >
            <img src="/static/img/icon/heroicons/check-green.svg" class="w-5 h-5 mr-2" />
            Save
          </button>
          <button
            class="bg-main-button rounded bg-opacity-10 text-button flex justify-center items-center focus:outline-none py-2 px-4 border-button"
            style="border-width: 1px;"
            @click="$refs.avatar.click()"
          >
            <img src="/static/img/icon/heroicons/plus-green.svg" class="w-5 h-5 mr-2" />
            Change
          </button>
        </div>
      </div>
    </modal>
  </div>
</template>


<script>
// import Button from '@/components/Ui/Button.vue'
// import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Input from '@/components/Ui/Input.vue'
import MySwitch from '@/components/Ui/MySwitch.vue'
import { Cropper } from 'vue-advanced-cropper';
import CountrySelect from '@/components/Ui/CountrySelect.vue'


export default {
  components: {
    // Button,
    // CardEditContent,
    Input,
    MySwitch,
    Cropper,
    CountrySelect
  },
  data() {
    return {
      form: {},
      defaultAvatar: '/static/img/icon/heroicons/camera-e2e8f0.svg',
      formBackup: ''
    }
  },
  methods: {
    onSubmit() {
      this.$store.dispatch('resumes/update')  
      this.$router.push('/workspace/resume/content')
    },
    onCancel () {
      this.$store.commit('rcontent/setPersonal', JSON.parse(this.formBackup))
      this.$router.push('/workspace/resume/content')
    },
    previewFile() {
      const file = this.$refs.avatar.files[0];
      const reader = new FileReader();

      reader.addEventListener("load", () => {
        this.form.originalAvatar = reader.result;
      }, false);

      if (file) {
        reader.readAsDataURL(file);
        this.$refs.avatar.value = ''
      }
      this.show()
    },
    show() {
      this.$modal.show('AvatarModal')
    },
    hide() {
      this.$modal.hide('AvatarModal')
    },
    changeStencil (stencil) {
      this.form.stencil = stencil
    },
    saveImage () {
      const { canvas } = this.$refs.cropper.getResult();
      this.form.avatar = canvas.toDataURL();
      this.hide()
    },
    addSocialField (index) {
      this.form.addedSocialFields.push({
        key: this.form.socialFields[index],
        value: '',
        index: index
      })
      this.form.socialFields.splice(index, 1)
    },
    removeSocialField (index) {
      let field = this.form.addedSocialFields.splice(index, 1)[0]
      this.form.socialFields.splice(field.index, 0, field.key)
    },
    avatarClick () {
      if (this.editAvatar || this.form.originalAvatar) {
        this.show()
      } else {
        this.$refs.avatar.click()
      }
    }
  },
  computed: {
    avatarClass: function () {
      return {
        'w-32': true,
        'h-w-32 border rounded-full': true,
        'p-6': this.avatarUrl == this.defaultAvatar ? true : false,
        'rounded-full': this.form.stencil === 'circle-stencil' ? true : false,
        'overflow-hidden': true,
      }
    },
    avatarUrl: function () {
      if (this.form.avatar) {
        return this.form.avatar
      }

      return this.defaultAvatar
    },
    editAvatar: function () {
      if (this.form.avatar) {
        return true
      }

      return false
    }
  },
  mounted() {
    this.form = this.$store.state.rcontent.content.personal
    this.formBackup = JSON.stringify(this.form)
  }
}
</script>
