<template>
  <!-- Setp wizard -->
  <div class="bg-white p-5 pb-0 shadow bottom-shadow sticky top-0 z-50">
    <div class="flex flex-col">
      <div class="flex justify-between">
        <div class="flex flex-col">
          <div class="flex items-center">
            <a v-if="currentStep>0" @click="prevClick(currentStep)" class="cursor-pointer hover:opacity-50">
              <img src="/static/img/icon/left-arrow.svg" class="inline">
            </a> 
            <a v-else @click="navigate" class="cursor-pointer hover:opacity-50">
              <img src="/static/img/icon/left-arrow.svg" class="inline">
            </a>
            <div class="flex flex-col ml-4">
              <h1 class="text-xl font-semibold text-primary-3">{{steps[currentStep]['title']}}</h1>
              <p class="text-primary-4 text-sm">{{steps[currentStep]['description']}}</p>
            </div>
          </div>
        </div>
        <!-- <div class="flex items-center">
          <button class="bg-primary-gradient rounded-md border w-full text-white flex text-base justify-center items-center px-5 py-1 hover:bg-secondary"
            @click="nextClick(currentStep)">
            Next <iconly name="arrow-right" type="light"/>
          </button>
        </div> -->
      </div>
    </div>
    <div class="container mx-auto mt-5">
      <div class="grid grid-cols-3">
        <a class="sm:px-2 py-3 col-span-1 sm:w-auto justify-center title-font text-sm text-center font-medium inline-flex items-center leading-none tracking-wider rounded-t cursor-pointer"
            v-for="(step, index) in steps" :key="index" :class="{
                'border-b-2 border-primary text-primary': index === currentStep
              }" @click="tabClick(index)">
          <iconly :name="step['icon']" type="light"/> <span class="ml-2">{{ step['title'] }}</span>
        </a>
      </div>
  </div>
  </div>
</template>

<script>

export default {
  name: 'Header',
  data() {
    return {
      steps: [
        // {step: 0, title: 'Select Template', description: 'Select template' , icon: 'bookmark'},
        {step: 0, title: 'Edit Content', description: 'Fill up your personal information below', icon: 'document'},
        {step: 1, title: 'Update Design', description: 'Stylize your resume below', icon: 'image'},
        {step: 2, title: 'Finalize', description: 'Finalize --- Lorem ipsum dolor sit amet.', icon: 'send'}
      ],
      currentStep: 0
    }
  },
  watch: {
    '$route': {
      handler: function(route) {
        if (route.path.includes('content')) {
          this.currentStep = 0;
        }
        if (route.path.includes('design')) {
          this.currentStep = 1;
        }
      },
      deep: true,
      immediate: true
    }
  },
  computed: {
  },
  methods: {
    navigateToURL: function(step) {
     let activeStep = step;
     switch(activeStep) {
      //  STEP: 0 ---Content section
       case 0: {
          this.contentUrl();
          break;
       }
      //  STEP: 1 ---Design section
        case 1: {
          this.designUrl();
          break;
       }
      //  STEP: 2 ---Finalize section
        case 2: {
          this.finalizeUrl();
          break;
       }
       default: {
          this.contentUrl();
          break;
       }
     }
   },
    contentUrl: function () {
      let type = ''
      if (this.$route.path.includes('coverletter')) {
        type = 'coverletter'
      }
      if (this.$route.path.includes('resume')) {
        type = 'resume'
      }
      this.$router.push('/workspace/' + type + '/content');  
    },
    designUrl: function () {
      let type = ''
      if (this.$route.path.includes('coverletter')) {
        type = 'coverletter'
      }
      if (this.$route.path.includes('resume')) {
        type = 'resume'
      }
      this.$router.push('/workspace/' + type + '/design');
    },
    finalizeUrl: function () {
      let type = ''
      if (this.$route.path.includes('coverletter')) {
        type = 'coverletter'
      }
      if (this.$route.path.includes('resume')) {
        type = 'resume'
      }
      this.$router.push('/workspace/' + type + '/finalize');
    },
    navigate: function() {
      let type = ''
      if (this.$route.path.includes('coverletter')) {
          type = 'coverletter'
      }
      if (this.$route.path.includes('resume')) {
          type = 'resume'
      }
      this.$router.push('/templates/' + type);  
    },
    // nextClick(activeStep) {
    //   if(activeStep < this.steps.length-1) {
    //     this.currentStep = activeStep + 1;
    //     this.navigateToURL(this.currentStep);
    //   }
    // },
    prevClick(activeStep) {
      if(activeStep > 0)
      {
        this.currentStep = activeStep - 1;
        this.navigateToURL(this.currentStep);
      }
    },
    tabClick(step) {
      this.currentStep = step;
      this.navigateToURL(this.currentStep);
    }
  }
};
</script>

