<template>
  <div :style="divStyle" id="divpdf">
    <div :style="pageStyle" id="pdfpreview">
      <div :style="headerStyle">
        <div>
          <img 
            v-if="$store.state.rcontent.content.personal.avatar" 
            :style="imgStyleComputed" 
            :src="$store.state.rcontent.content.personal.avatar" 
          />
          <p :style="nameStyle">
            {{ $store.state.rcontent.content.personal.firstname }}
            {{ $store.state.rcontent.content.personal.lastname}}
          </p>
          <p style="font-size: 22px;font-style: italic;font-weight: 400;">
            {{ $store.state.rcontent.content.personal.jobtitle }}
          </p>
          <ul style="padding-top: 15px;">
            <li 
            v-if="addressCompunted"  
            style="display: inline-block;font-size: 15px;margin: 0px 6px;">
              <svg style="width: 1rem; height: 1rem; display: inline-block" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"></path><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"></path></svg>
              {{ $store.state.rcontent.content.personal.state }}
              {{ $store.state.rcontent.content.personal.country && 
                $store.state.rcontent.content.personal.state ? ', ' : '' }}
              {{ $store.state.rcontent.content.personal.country }}
            </li>
            <li 
            v-if="$store.state.rcontent.content.personal.email"
            style="display: inline-block;font-size: 15px;margin: 0px 6px;">
              <svg style="width: 1rem; height: 1rem; display: inline-block" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"></path></svg>
              {{ $store.state.rcontent.content.personal.email }}
            </li> 
            <li 
            v-if="$store.state.rcontent.content.personal.phone"
            style="display: inline-block;font-size: 15px;margin: 0px 6px;">
              <svg style="width: 1rem; height: 1rem; display: inline-block" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z"></path></svg>
              {{ $store.state.rcontent.content.personal.phone }}
            </li> 
          </ul>
        </div>
      </div>
      <profile 
        :content="$store.state.rcontent.content.profile"
        :sectionTitleStyle="sectionTitleStyle"
        :bodyStyle="bodyStyle"
        @onUpdate="updatePageBreak"
      />
      <template
        v-for="content in $store.state.rcontent.content.addedContents"
      >
        <component 
          :key="content.id" 
          :is="content.id" 
          :content="content"
          :sectionTitleStyle="sectionTitleStyle"
          :bodyStyle="bodyStyle"
          @onUpdate="updatePageBreak"
        />
      </template>
    </div>
  </div>
</template>

<script>
import { debounce, pagebreak, empty } from '@/utils'
import sections from '@/template/resume/resume1section'

export default {
  components: {
    ...sections
  },
  data () {
    return {
      divStyle: {
        width: '794px',
        background: 'white',
        display: 'block',
        boxShadow: '0 0 0.5cm rgb(0 0 0 / 50%)'
      },
      pageStyle: {
        fontFamily: "'Montserrat', sans-serif",
        lineHeight: '1.15',
      },
      headerStyle: {
        paddingTop: '40px',
        paddingBottom: '30px',
        backgroundColor: '#862E3D',
        color: '#fff',
        textAlign: 'center',
      },
      bodyStyle: {
        paddingTop: '15px',
        paddingLeft: '60px',
        paddingRight: '60px',
      },
      imgStyle: {
        height: '110px',
        width: '110px',
        margin: 'auto',
      },
      nameStyle: {
        paddingTop: '20px',
        fontSize: '32px',
        paddingBottom: '6px',
        fontWeight: 600,
      },
      sectionTitleStyle: {
        fontSize: '18px',
        fontWeight: 600,
        color: '#862E3D',
        borderBottom: '3px solid #862E3D',
        paddingBottom: '3px',
      },
      height: 1106,
    }
  },
  methods: {
    empty,
    updatePageBreak: function () {
      debounce(
        pagebreak(
          this.height,
          37,
          60
        ),
        150
      )
    },
  },
  computed: {
    imgStyleComputed() {
      return {
        ...this.imgStyle,
        borderRadius: this.$store.state.rcontent.content.personal.stencil == 'circle-stencil' ? '50%' : '0%',
      }
    },
    addressCompunted() {
      return (
        this.$store.state.rcontent.content.personal.state +
        this.$store.state.rcontent.content.personal.country
      )
    },
  },
  updated: function () {
    this.updatePageBreak()
  },
  mounted: function () {
    this.updatePageBreak()
  },
}
</script>