import { resume } from '../../api';
import store from '@/store'

const state = () => ({
  resumes: [],
  cachedresume: '',
})

const actions = {
  async getall({ commit }) {
    const resumes = await resume.getall()
    commit('setResumes', resumes)
    return
  },
  async get({ commit }, payload) {
    commit('setCachedresume', payload)
    return await resume.get(payload)
  },
  async create() {
    return await resume.create()
  },
  async update({ state }) {
    if (store.state.user.loggedIn && localStorage.selectedresume) {
      resume.update({
        resumeName: "my resume",
        templateId: store.state.main.resumetemplates.indexOf(store.state.main.previewtemplate),
        resumeJson: JSON.stringify({
          content: store.state.rcontent.content
        }),
        resumeId: state.cachedresume
      })
    }
  }
}

const mutations = {
  setResumes (state, resumes) {
    state.resumes = resumes
  },
  setCachedresume (state, idx) {
    state.cachedresume = idx
  }
}

export default {
  namespaced: true,
  actions,
  state,
  mutations
}