<template>
  <div :style="divStyle" id="divpdf">
    <div :style="pageStyle" id="pdfpreview">
      <div :style="headerStyle">
        <div>
          <p :style="nameStyle">
            {{ this.$store.state.rcontent.content.personal.firstname }}
            {{ this.$store.state.rcontent.content.personal.lastname}}
          </p>
          <p style="font-size: 24px; font-weight: 500; opacity: .8">
            {{ this.$store.state.rcontent.content.personal.jobtitle}} 
          </p>
          <ul style="padding-top: 8px; list-style-type: none;">
            <li
              style="display: inline-block;font-size: 16px;font-weight: 500;opacity: .8;margin: 6px;"
            >
              {{ this.$store.state.rcontent.content.personal.state }}
              {{ this.$store.state.rcontent.content.personal.country && 
                this.$store.state.rcontent.content.personal.state ? ', ' : '' }}
              {{ this.$store.state.rcontent.content.personal.country }}
              <span 
              v-if="addressCompunted && (this.$store.state.rcontent.content.personal.email || this.$store.state.rcontent.content.personal.phone)"
              style="margin-left: 8px; display: inline-block;"
              >
                |
              </span>
            </li>
            <li
              style="display: inline-block;font-size: 16px;font-weight: 500;opacity: .8;margin: 6px;"
            >
              {{ this.$store.state.rcontent.content.personal.email }}
              <span 
              v-if="this.$store.state.rcontent.content.personal.email && this.$store.state.rcontent.content.personal.phone"
              style="margin-left: 8px; display: inline-block;"
              >
                |
              </span>
            </li>
            <li
              style="display: inline-block;font-size: 16px;font-weight: 500;opacity: .8;margin: 6px;"
            >
              {{ this.$store.state.rcontent.content.personal.phone }}
            </li>
          </ul>
        </div>
      </div>
      <profile 
        :content="$store.state.rcontent.content.profile"
        :sectionTitleStyle="sectionTitleStyle"
        :bodyStyle="bodyStyle"
        @onUpdate="updatePageBreak"
      />
      <template
        v-for="content in $store.state.rcontent.content.addedContents"
      >
        <component 
          :key="content.id" 
          :is="content.id" 
          :content="content"
          :sectionTitleStyle="sectionTitleStyle"
          :bodyStyle="bodyStyle"
          @onUpdate="updatePageBreak"
        />
      </template>
    </div>
  </div>
</template>

<script>
import { debounce, pagebreak, empty } from '@/utils'
import sections from '@/template/resume/resume2section'

export default {
  components: {
    ...sections
  },
  data () {
    return {
      divStyle: {
        width: '794px',
        background: 'white',
        display: 'block',
        boxShadow: '0 0 0.5cm rgb(0 0 0 / 50%)'
      },
      pageStyle: {
        fontFamily: "'Montserrat', sans-serif",
        lineHeight: '1.15',
      },
      headerStyle: {
        paddingTop: '40px',
        paddingBottom: '40px',
        backgroundColor: '#3E7A69',
        color: '#fff',
        textAlign: 'center',
      },
      bodyStyle: {
        paddingTop: '15px',
        paddingLeft: '40px',
        paddingRight: '40px',
      },
      nameStyle: {
        fontSize: '42px',
        paddingBottom: '5px',
        fontWeight: 500,
      },
      sectionTitleStyle: {
        fontSize: '18px',
        fontWeight: 600,
        color: '#3E7A69',
        backgroundColor: '#EDF6F5',
        textAlign: 'center',
        padding: '5px 0',
      },
      height: 1106,
    }
  },
  methods: {
    empty,
    updatePageBreak: function () {
      debounce(
        pagebreak(
          this.height,
          37,
          40
        ),
        150
      )
    },
  },
  computed: {
    addressCompunted() {
      return (
        this.$store.state.rcontent.content.personal.state +
        this.$store.state.rcontent.content.personal.country
      )
    },
  },
  updated: function () {
    this.updatePageBreak()
  },
  mounted: function () {
    this.updatePageBreak()
  },
}
</script>