<template>
  <div>
    <input
      class="checkbox-tools"
      type="radio"
      :name="name"
      :id="id"
      :checked="isChecked"
    />
    <label class="for-checkbox-tools" :for="id">
      <img
        v-if="icon"
        :src="icon"
        :class="getClasses"
      />
      <div class="flex w-full" :class="[showRadioIcon?'justify-start':'justify-center']">
        <div v-if="showRadioIcon" class="outer-circle w-4 h-4 border rounded-full flex justify-center items-center mr-2">
          <div class="w-3 h-3 rounded-full inner-circle"></div>
        </div>
        <div class="flex flex-col items-center">
          <h1 v-if="label">{{ label }}</h1>
          <p v-if="description">{{description}}</p>
        </div>
      </div>
    </label>
  </div>
</template>

<script>
export default {
  name: 'Radio',
  props: ['label', 'description', 'icon', 'name', 'id', 'isChecked', 'className', 'showRadioIcon'],
  computed: {
    getClasses: function () {

      let classes = "mr-3 "

      if (this.className) {
        classes += this.className
      }
      return classes.trim()
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
[type='radio']:checked,
[type='radio']:not(:checked) {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}
.checkbox-tools:checked + label,
.checkbox-tools:not(:checked) + label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  font-weight: 400;
  margin: 0 auto;
  border-radius: 5px;
  cursor: pointer;
  color: $primary;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
.checkbox-tools:not(:checked) + label {
  background-color: #ffffff;
  color: #686c70;
  border: 1px solid #E5E5E5;
  .outer-circle {
    border-color: #8E8E8E;
  }
}
.checkbox-tools:checked + label {
  background-color: rgba($primary, 0.12);
  border: 1px solid rgba($primary, 0.32);
  color: $primary;
  .outer-circle {
    border-color: $primary;
    .inner-circle {
      background: $primary;
    }
  }
}
</style>
