<template>
  <div>
    <div class="bg-white rounded flex flex-col cursor-pointer card-shadows relative z-10">
      <div
        @click="openContent"
        class="flex md:flex-no-wrap flex-wrap p-6 justify-between items-center"
      >
        <div class="flex items-center space-x-4 md:mb-0 mb-3">
          <img :src="section.icon" class="h-8" />
          <div>
            <div class="flex">
              <h1 v-show="!rename" class="text-primary-1 text-lg font-semibold">
                {{ section.titleAlias }}
              </h1>
              <div v-show="open && rename" class="flex">
                <input
                  type="text"
                  class="bg-white p-1 border-gray-400 rounded focus:outline-none border-2 w-3/6 focus:border-button"
                  @click.stop=""
                  v-model="sectionAlias"
                  @keyup="renameSectionAlias"
                />
                <button
                  class="bg-main-button rounded bg-opacity-10 text-button flex justify-center items-center focus:outline-none px-1 border-button mx-1"
                  style="border-width: 1px;"
                  @click.stop="rename = false"
                >
                  <img src="/static/img/icon/heroicons/check-green.svg" class="w-5 h-5 mx-1" />
                </button>
                <button
                  class="rounded bg-opacity-10 flex justify-center items-center focus:outline-none px-1 border-2 remove-btn bg-gray-300"
                  @click.stop="closeSectionAlias"
                >
                  <img src="/static/img/icon/heroicons/close.svg" class="w-5 h-5 mx-1" />
                </button>
              </div>
              <button 
                v-show="open && !rename" 
                class="rounded bg-opacity-10 flex justify-center items-center focus:outline-none px-2 border-2 ml-3 remove-btn bg-gray-300"
                @click.stop="rename = true; tempAlias = section.titleAlias"
              >
                <img src="/static/img/icon/heroicons/pencil.svg" class="w-5 h-5 mr-2"> 
                Rename 
              </button>
            </div>
            <p class="text-primary-2 text-xs">{{ section.text }}</p>
          </div>
        </div>
        <img
          v-imgSrc="`icon/ico-chevron-up.svg`"
          class="transform w-4 h-4"
          :class="[open ? 'rotate-0' : 'rotate-180']"
          style="margin-top: -53px;"
        />
      </div>
      <div v-show="open" class>
        <draggable
          v-bind="dragOptions"
          @end="dragStop"
          v-model="section.data"
        >
          <div
            v-for="(data, index) in section.data"
            :key="index"
            class="flex items-center border-t p-6 bg-white hover:cursor-move"
          >
            <div class="flex md:flex-no-wrap flex-wrap w-full">
              <div class="pl-8 w-60p">
                <component :is="previewComponent" :data="data" />
              </div>
              <div class="w-30p justify-end flex h-80p text-right ml-auto">
                <button
                  @click="$router.push(`/workspace/resume/content/${section.id}/${index}`)"
                  class="bg-white rounded border py-2 border-edit px-4 flex items-center justify-center"
                >
                  <img src="/static/img/icon/heroicons/edit.svg" width="16"/>
                </button>
                <button
                  class="bg-white rounded border py-2 border-edit px-4 flex items-center justify-center ml-4"
                  @click="removeEntity(index)"
                >
                  <img src="/static/img/icon/heroicons/trash.svg" width="16"/>
                </button>
              </div>
            </div>
          </div>
        </draggable>
        <div class="flex items-center justify-center border-t p-6">
          <button
            class="bg-main-button rounded bg-opacity-10 text-button flex justify-center items-center focus:outline-none py-2 px-4 border-button"
            style="border-width: 1px;"
            @click="addSectionAndRedirect()"
          >
            <img src="/static/img/icon/heroicons/plus-green.svg" class="w-5 h-5 mr-2" />
            Add
          </button>
          <button
            class="rounded bg-opacity-10 flex justify-center items-center focus:outline-none py-2 px-4 border-2 ml-3 remove-btn bg-gray-300"
            @click="removeContent(section.id)"
          >
            <img src="/static/img/icon/heroicons/trash.svg" class="w-5 h-5 mr-2" />
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import draggable from 'vuedraggable'
import previews from '@/components/Resume/PreviewContent'

export default {
  name: 'NewCardContent',
  props: {
    section: {
      type: Object,
      required: true
    }
  },
  components: {
    draggable,
    ...previews,
  },
  data() {
    return {
      rename: false,
      sectionAlias: this.section.titleAlias,
      tempAlias: ''
    }
  },
  watch: {
    open: function () {
      this.rename = false
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: 'description',
        disabled: false,
        ghostClass: 'ghost'
      }
    },
    previewComponent() {
      let comp = this.section.id
      return "Preview" + comp[0].toUpperCase() + comp.substring(1);
    },
    open() {
      if (this.$store.state.rcontent.content.defaultOpen == this.section.id) {
        return true
      }
      return false
    }
  },
  methods: {
    removeContent(id) {
      this.$store.commit('rcontent/removeContent', id)
      this.$store.dispatch('resumes/update')
    },
    removeEntity(index) {
      this.$store.commit('rcontent/removeContentByIndex', {
        key: this.section.id,
        index: index
      })
      this.$store.dispatch('resumes/update')
    },
    dragStop() {
      this.$store.commit('rcontent/updateContentOrder', {
        key: this.section.id,
        data: this.section.data
      })
      this.$store.dispatch('resumes/update')
    },
    openContent() {
      if (this.open) {
        this.$store.commit('rcontent/setDefaultOpen', '')
      } else {
        this.$store.commit('rcontent/setDefaultOpen', this.section.id)
      }
    },
    renameSectionAlias() {
      this.$store.commit('rcontent/renameSectionAlias', {
        key: this.section.id,
        value: this.sectionAlias
      })
    },
    closeSectionAlias() {
      this.rename = false;
      this.sectionAlias = this.tempAlias;
      this.$store.commit('rcontent/renameSectionAlias', {
        key: this.section.id,
        value: this.sectionAlias
      })
    },
    addSectionAndRedirect() {
      let id = this.$store.state.rcontent.content.addedContents[this.section.id].data.length
      this.$store.commit('rcontent/addContentData', this.section.id)
      this.$router.push(this.section.redirect + '/' + id)
    }
  },
}
</script>
<style lang="scss" scoped>
.ghost {
  opacity: 0.1;
  background: gray;
}
.remove-btn {
  color: #2b2c2d;
  border: 1px solid #76787b;
}
</style>
