<template>
  <label :for="id" class="flex items-center cursor-pointer">
    <div class="mr-4 font-normal text-primary-1 text-sm" v-if="label">
       {{ label }}
    </div>
    <div class="relative flex items-center">
      <input
        :value="val"
        v-model="checked"
        @change="onChange"
        class="toggle-checkbox"
        type="checkbox"
        :id="id"
        :name="name">
      <div class="block bg-primary-light border order-primary-light w-14 h-6 rounded-full"></div>
      <div class="dot absolute left-1 top-1 bg-white w-4 h-4 rounded-full transition"></div>
    </div>
  </label>
</template>
<script>
export default {
    name: 'Toggle',
    props: ['label', 'name', 'id', 'value', 'val'],
    data() {
        return {
        checkedProxy: false
        }
    },
    computed: {
        checked: {
        get() {
            return this.value
        },
        set(val) {
            this.checkedProxy = val
        }
        }
    },
    methods: {
        onChange: function () {
        this.$emit('input', this.checkedProxy)
        }
    }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
.toggle-checkbox ~ .dot {
    transition-duration: 0.5s;
    transform: translateX(20%);
}
.toggle-checkbox:checked ~ .dot {
    transition-duration: 0.5s;
    transform: translateX(223%);
    background-color: $primary;
}
</style>
