<template>
  <div class="pb-12 pr-5 pl-6">

    <div class="mt-12 flex flex-col space-y-4">
      <CardEditContent title="Create Organization" icon="/static/img/icons-primary/ShieldDone.svg">
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Organization <span class="text-button">*</span>
          </h1>
          <Input v-model="form.name" placeholder="Enter organization name" />
        </div>

        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Position
            <span class="float-right text-primary-2 text-sm font-light"
              >(Optional)</span
            >
          </h1>
          <Input v-model="form.position" placeholder="Enter position" />
        </div>

        <div class="grid grid-cols-2 gap-x-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              City
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.city" placeholder="Enter city" />
          </div>
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Country
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <CountrySelect v-model="form.country" placeholder="Select Country" />
          </div>
        </div>

        <div class="grid grid-cols-2 gap-x-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Start Date
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <div class="grid grid-cols-5 gap-x-3">
              <div class="col-span-5">
                <DatePicker 
              type="month" 
              value-type="format" 
              format="MM/YYYY" 
              @change="changeStartDate">
                <template v-slot:input>
                  <Input
                    v-model="form.startDate"
                    placeholder="Month / Year"
                  />
                </template>
                <template v-slot:icon-calendar>
                  {{  }}
                </template>
              </DatePicker>
              </div>
              <div class="col-span-5 mt-8">
                <Checkbox
                  label="Don't show"
                  name="header-position"
                  id="header-position-1"
                  isChecked
                />
                <Checkbox
                  label="Only year"
                  class="mt-5"
                  name="header-position"
                  id="header-position-2"
                />
              </div>
            </div>
          </div>

          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              End Date
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <div class="grid grid-cols-5 gap-3">
              <div class="col-span-5">
                <DatePicker 
              type="month" 
              value-type="format" 
              format="MM/YYYY" 
              @change="changeEndDate">
                <template v-slot:input>
                  <Input
                    v-model="form.endDate"
                    placeholder="Month / Year"
                  />
                </template>
                <template v-slot:icon-calendar>
                  {{  }}
                </template>
              </DatePicker>
              </div>
              <div class="mt-5 col-span-5">
                <Checkbox
                  label="Present (Current)"
                  name="header-position"
                  id="header-position-3"
                />
                <Checkbox
                  label="Don't show"
                  class="mt-5"
                  name="header-position"
                  id="header-position-4"
                />
                <Checkbox
                  label="Only year"
                  class="mt-5"
                  name="header-position"
                  id="header-position-5"
                />
              </div>
            </div>
          </div>
        </div>

        <div>
          <h1 class="font-semibold text-primary-1 text-base mb-6">
            Description
            <span class="float-right text-primary-2 text-sm font-light"
              >(Optional)</span
            >
          </h1>
          <TextEditor v-model="form.description" @editorChange="editorData" />
        </div>
      </CardEditContent>

      <div class="flex items-center justify-end space-x-5">
        <button
          @click="onCancel"
          class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
        >
          Cancel
        </button>
        <Button @click.native="onSubmit" value="Save" />
      </div>
    </div>
  </div>
</template>


<script>
import Button from '@/components/Ui/Button.vue'
import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Checkbox from '@/components/Ui/Checkbox.vue'
import Input from '@/components/Ui/Input.vue'
import TextEditor from '@/components/Ui/TextEditor.vue'
import DatePicker from 'vue2-datepicker';
import CountrySelect from '@/components/Ui/CountrySelect.vue'


export default {
  components: {
    Button,
    CardEditContent,
    Checkbox,
    Input,
    TextEditor,
    DatePicker,
    CountrySelect
  },
  data: function () {
    return {
      form: {},
      formBackup: '',
      index: -1,
    }
  },
  methods: {
    editorData (data) {
      this.form.description = data
    },
    onCancel() {
      let form = JSON.parse(this.formBackup)
      for (const key in form) {
        this.form[key] = form[key]
      }
      this.$router.push('/workspace/resume/content')
    },
    changeStartDate(date) {
      this.form.startDate = date
    },
    changeEndDate(date) {
      this.form.endDate = date
    },
    onSubmit () {
      this.$store.dispatch('resumes/update')
      this.$router.push('/workspace/resume/content')
    }
  },
  mounted() {
    this.index = this.$route.params.id
    this.form = this.$store.state.rcontent.content.addedContents.organizations.data[this.index]
    this.formBackup = JSON.stringify(this.form)
  },
  beforeDestroy () {
    if (!this.form.name) {
      this.$store.commit('rcontent/removeContentByIndex', {
        key: 'organizations',
        index: this.index
      })
    }
  }
}
</script>
