<template>
  <div class="pb-12 pr-5 pl-6">

    <div class="mt-12 flex flex-col space-y-4">
      <CardEditContent title="Create Course" icon="/static/img/icons-primary/Activity.svg">
        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Course Name <span class="text-button">*</span>
          </h1>
          <InputIcon v-model="form.name" icon="/static/img/icons-primary/Activity.svg" placeholder="Enter course Name" />
        </div>

        <div class="grid grid-cols-2 gap-x-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Institution
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.institute" placeholder="Enter institution" />
          </div>
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Achievment
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.achievement" placeholder="Enter Achievment" />
          </div>
        </div>

        <div class="grid grid-cols-2 gap-x-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              City
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <Input v-model="form.city" placeholder="Enter city" />
          </div>
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Country
              <span class="float-right text-primary-2 text-sm font-light"
                >(Optional)</span
              >
            </h1>
            <CountrySelect v-model="form.country" placeholder="Select Country" />
          </div>
        </div>

        <div class="grid grid-cols-2 gap-5">
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              Start Date
            </h1>
            <div class="flex items-center gap-x-5">
              <DatePicker 
              type="month" 
              value-type="format" 
              format="MM/YYYY" 
              @change="changeStartDate">
                <template v-slot:input>
                  <Input
                    v-model="form.startDate"
                    placeholder="Month / Year"
                  />
                </template>
                <template v-slot:icon-calendar>
                  {{  }}
                </template>
              </DatePicker>
              <MySwitch
                v-model="form.showToogle"
                id="startDateToogle"
                val="startDateToogle"
              />
            </div>
          </div>
          <div>
            <h1 class="font-medium text-primary-1 text-sm mb-2">
              End Date
            </h1>
            <div class="flex items-center gap-x-5">
              <DatePicker 
              type="month" 
              value-type="format" 
              format="MM/YYYY" 
              @change="changeEndDate">
                <template v-slot:input>
                  <Input
                    v-model="form.endDate"
                    placeholder="Month / Year"
                  />
                </template>
                <template v-slot:icon-calendar>
                  {{  }}
                </template>
              </DatePicker>
              <MySwitch
                v-model="form.showToogle"
                id="endDateToogle"
                val="endDateToogle"
              />
            </div>
          </div>
          <div></div>
          <div>
            <Checkbox
              v-model="form.selectedItems"
              label="Present"
              val="1" id="1"
            />
          </div>
        </div>

        <div>
          <h1 class="font-medium text-primary-1 text-sm mb-2">
            Description
            <span class="float-right text-primary-2 text-sm font-light"
              >(Optional)</span
            >
          </h1>
          <TextEditor v-model="form.description" @editorChange="editorData" />
        </div>

        <!-- <button
          class="bg-main-button rounded w-full bg-opacity-10 text-button flex justify-center items-center focus:outline-none p-4 border-2 border-button"
        >
          Add More
          <img src="/static/img/icon/heroicons/plus-green.svg" class="w-6 h-6" />
        </button> -->
      </CardEditContent>

      <div class="flex items-center justify-end space-x-5">
        <button
          @click="onCancel"
          class="no-filled button-primary rounded-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
        >
          Cancel
        </button>
        <Button @click.native="onSubmit" value="Save" />
      </div>
    </div>
  </div>
</template>


<script>
import Button from '@/components/Ui/Button.vue'
import CardEditContent from '@/components/Ui/CardEditContent.vue'
import Checkbox from '@/components/Ui/Checkbox.vue'
import Input from '@/components/Ui/Input.vue'
import TextEditor from '@/components/Ui/TextEditor.vue'
import InputIcon from '@/components/Ui/InputIcon.vue'
import MySwitch from '@/components/Ui/MySwitch.vue'
import DatePicker from 'vue2-datepicker';
import CountrySelect from '@/components/Ui/CountrySelect.vue'


export default {
  components: {
    Button,
    CardEditContent,
    Checkbox,
    Input,
    InputIcon,
    TextEditor,
    MySwitch,
    DatePicker,
    CountrySelect
  },
  data: function () {
    return {
      form: {},
      formBackup: '',
      index: -1,
    }
  },
  methods: {
    changeStartDate(date) {
      this.form.startDate = date
    },
    changeEndDate(date) {
      this.form.endDate = date
    },
    editorData (data) {
      this.form.description = data
    },
    onCancel() {
      let form = JSON.parse(this.formBackup)
      for (const key in form) {
        this.form[key] = form[key]
      }
      this.$router.push('/workspace/resume/content')
    },
    onSubmit () {
      this.$store.dispatch('resumes/update')
      this.$router.push('/workspace/resume/content')
    }
  },
  mounted() {
    this.index = this.$route.params.id
    this.form = this.$store.state.rcontent.content.addedContents.courses.data[this.index]
    this.formBackup = JSON.stringify(this.form)
  },
  beforeDestroy () {
    if (!this.form.name) {
      this.$store.commit('rcontent/removeContentByIndex', {
        key: 'courses',
        index: this.index
      })
    }
  }
}
</script>
