<template>
  <CardDesign
    :title="title"
    icon="/static/img/icon/heroicons/identification.svg?v=3"
  >
    <div>
      <!-- <h1 class="font-medium text-primary-1 text-sm mb-2">Size</h1> -->

      <RadioGroup
        :objectkey="objectkey + '.size'"
        :items="sizes"
        :defaultValue="$store.state.cldesign.design.font.name.size"
        :customclass="'grid grid-cols-' + sizes.length + ' gap-x-5'"
      />
    </div>
    <div>
      <RangeSlider
        :data="data"
        :range="range"
        postfix=""
        :defaultValue="defaultweight"
        :objectkey="objectkey + '.weight'"
      />
    </div>
    <Select :value="fontfamilymodel" @input="updateFontfamily">
      <Option
        v-for="fontfamily in fontfamilies"
        :key="fontfamily.value"
        :value="fontfamily.value"
      >
        {{ fontfamily.label }}
      </Option>
    </Select>
    <div class="grid grid-cols-4 gap-x-1">
      <RadioGroup
        :objectkey="objectkey + '.texttransform'"
        :items="texttransforms"
        :defaultValue="defaultTransform"
        customclass="col-span-3 grid grid-cols-3 mt-5 gap-x-1"
        
      />
      <CheckboxNew
        :objectkey="objectkey + '.fontstyle'"
        label="Italic"
        truevalue="italic"
        falsevalue="normal"
        customclass="mt-5"
      />
    </div>
    <div>
      <!-- <h1 class="font-medium text-primary-1 text-sm mb-2">Color</h1> -->
      <RadioColorsGroup
        customclass="grid grid-cols-10 mt-5 gap-3"
        :items="contentColors"
        :objectkey="objectkey + '.color'"
        :defaultValue="colorValue"
      />
    </div>
  </CardDesign>
</template>

<script>
import RangeSlider from '@/components/Ui/RangeSlider.vue'
import CardDesign from '@/components/Design/CardDesign.vue'
import RadioGroup from '@/components/Ui/RadioGroup.vue'
import Select from '@/components/Ui/Select.vue'
import CheckboxNew from '@/components/Ui/CheckboxNew.vue'
import RadioColorsGroup from '@/components/Ui/RadioColorsGroup.vue'

export default {
  components: {
    RangeSlider,
    CardDesign,
    Select,
    RadioGroup,
    CheckboxNew,
    RadioColorsGroup
  },
  props: [
    'title',
    'objectkey',
    'sizes',
    'defaultsize',
    'defaulValue',
    'defaultweight',
    'defaultstyle',
    'initalValue',
    'defaultTransform'
  ],
  data: () => ({
    data: [100, 200, 300, 400, 500, 600, 700, 800, 900],
    range: [
      {
        label: 100
      },
      {
        label: 200
      },
      {
        label: 300
      },
      {
        label: 400
      },
      {
        label: 500
      },
      {
        label: 600
      },
      {
        label: 700
      },
      {
        label: 800
      },
      {
        label: 900
      }
    ],
    texttransforms: [
      {
        label: 'None',
        value: 'none'
      },
      {
        label: 'Capitalize',
        value: 'capitalize'
      },
      {
        label: 'Uppercase',
        value: 'uppercase'
      }
    ],
    contentColors: [
      '#000000',
      '#D32F2F',
      '#C2185B',
      '#7B1FA2',
      '#512DA8',
      '#303F9F',
      // '#1976D2',
      '#0288D1',
      '#0097A7',
      '#00796B'
    ],
    fontfamilies: [
      {
        label: '-- Select Font --',
        value: ''
      },
      {
        label: 'Arial',
        value: 'Arial, sans-serif'
      },
      {
        label: 'Verdana',
        value: 'Verdana, sans-serif'
      },
      {
        label: 'Helvetica',
        value: 'Helvetica, sans-serif'
      },
      {
        label: 'Tahoma',
        value: 'Tahoma, sans-serif'
      },
      {
        label: 'Trebuchet MS',
        value: "'Trebuchet MS', sans-serif"
      },
      {
        label: 'Times New Roman',
        value: "'Times New Roman', serif"
      },
      {
        label: 'Georgia',
        value: 'Georgia, serif'
      },
      {
        label: 'Garamond',
        value: 'Garamond, serif'
      },
      {
        label: 'Courier New',
        value: "'Courier New', monospace"
      },
      {
        label: 'Brush Script MT',
        value: "'Brush Script MT', cursive"
      },
      {
        label: 'Open Sans',
        value: "'Open Sans', sans-serif"
      },
      {
        label: 'PT Serif',
        value: "'PT Serif', serif"
      },
      {
        label: 'Quicksand',
        value: "'Quicksand', sans-serif"
      },
      {
        label: 'Roboto',
        value: "'Roboto', sans-serif"
      },
      {
        label: 'Rubik',
        value: "'Rubik', sans-serif"
      },
      {
        label: 'Source Sans Pro',
        value: "'Source Sans Pro', sans-serif"
      },
      {
        label: 'Source Serif Pro',
        value: "'Source Serif Pro', serif"
      }
    ]
  }),
  computed: {
    rangeValue() {
      return eval(`this.$store.state.cldesign.${this.objectkey}.weight`)
    },
    textTransforms() {
      return eval(`this.$store.state.cldesign.${this.objectkey}.texttransform`)
    },
    colorValue() {
      return eval(`this.$store.state.cldesign.${this.objectkey}.color`)
    },
    fontfamilymodel() {
      return eval(`this.$store.state.cldesign.${this.objectkey}.fontfamily`)
    }
  },
  methods: {
    updateFontfamily(value) {
      this.$store.commit('cldesign/setDesignByKey', {
        key: this.objectkey + '.fontfamily',
        value: value
      })
    }
  }
}
</script>