<template>
<div class="p-5 pb-10 relative grid grid-cols-2 floating-actions">
    <div class="col-span-2 flex w-full justify-end">
        <transition name = "bounce">
            <div v-show="showActions" class="action-list grid grid-cols-4 gap-0 bg-white rounded-full h-14 text-xs" :class="{'isVisible':showActions}">
                <div class="action-item col-span-1 flex items-center justify-center border-r cursor-pointer rounded-full rounded-r-none"
                    @click="$store.commit('fullPreviewMode', false)">
                    <iconly name="swap" type="light" class="h-4 w-4 mr-2"/> Preview
                </div>
                <div class="action-item col-span-1 flex items-center justify-center border-r cursor-pointer">
                    <iconly name="send" type="light" class="h-4 w-4 mr-2"/> Share
                </div>
                <div class="action-item col-span-1 flex items-center justify-center border-r cursor-pointer">
                    <iconly name="ticksquare" type="light" class="h-4 w-4 mr-2"/> Save
                </div>
                <div class="action-item col-span-1 flex items-center justify-center cursor-pointer rounded-full rounded-l-none"
                    @click="downloadPdf">
                    <iconly name="download" type="light" class="h-4 w-4 mr-2"/> Download
                </div>
            </div>
        </transition>
        <div class="mx-5">
            <div class="flex items-center justify-center cursor-pointer h-14 w-14 rounded-full bg-primary revamp-shadow-2 on-hover-shadow" @click="showActions = !showActions">
                <img src="/static/img/icons-primary/icon-filter.svg">
                <!-- <iconly name="filter" type="light" class="text-white"/> -->
            </div>
        </div>
    </div> 
</div>
</template>

<script>
export default({
  name: 'ActionButton',
  props: {
  },
  data() {
    return {
        showActions: false
    }
  },
  methods: {
    downloadPdf () {
      let filename = []
      if (this.$route.path.includes('coverletter')) {
        if (this.$store.state.clcontent.content.personalDetail.firstName) {
          filename.push(this.$store.state.clcontent.content.personalDetail.firstName)
        }
        if (this.$store.state.clcontent.content.personalDetail.lastName) {
          filename.push(this.$store.state.clcontent.content.personalDetail.lastName)
        }
      }

      if (this.$route.path.includes('resume')) {
        if (this.$store.state.rcontent.content.personal.firstname) {
          filename.push(this.$store.state.rcontent.content.personal.firstname)
        }
        if (this.$store.state.rcontent.content.personal.lastname) {
          filename.push(this.$store.state.rcontent.content.personal.lastname)
        }
      }

      if (filename.length) {
        filename = filename.join('_')
      } else {
        if (this.$route.path.includes('resume')) {
          filename = 'resume'
        }
        
        if (this.$route.path.includes('coverletter')) {
          filename = 'cv'
        }
      }
      this.$store.state.fullScreenProgress = this.$loading.show();
      this.$store.dispatch('main/downloadpdf', {
        filename: filename,
      }).then(() => {
        this.$store.state.fullScreenProgress.hide()
      })
    }

  },
  computed: {
  }
})
</script>

<style lang="scss" scoped>
@import "@/assets/scss/revampCV/variables";
.floating-actions {
    width: 650px;
    .action-list {
        width: 460px;
        box-shadow: 0px 71px 171px rgba(0, 0, 0, 0.12), 0px 21.4044px 79.0092px rgba(0, 0, 0, 0.0781907), 0px 8.8903px 55.8503px rgba(0, 0, 0, 0.06), 0px 3.21545px 30.8639px rgba(0, 0, 0, 0.0418093);
    .action-item:hover {
        background: rgba(42, 175, 143, 0.22);
        }
    }
}
</style>