<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="n in skillCounter"
      :key="n"
    >
      <p v-if="n == 1" :style="sectionTitleStyle">
        <template v-for="(chunk, idx) in titleAliasChunks">
          <span :key="idx" style="font-size: 22px;">{{ chunk.first }}</span>{{ chunk.rest }} 
        </template>
      </p>
      <div style="display: flex;padding-top: 10px">
        <div style="width: 50%">
          <p style="font-size: 20px;font-weight: 600;">
            {{ content.data[(n-1) * 2].category }}
          </p>
          <p style="font-size: 15px;color: #848484;">
            <em>{{ skillString(content.data[(n-1) * 2].skills) }}</em>
          </p>
        </div>
        <div v-if="content.data.length >= n * 2" style="width: 50%">
          <p style="font-size: 20px;font-weight: 600;">
            {{ content.data[(n * 2) -1].category }}
          </p>
          <p style="font-size: 15px;color: #848484;">
            <em>{{ skillString(content.data[(n * 2) -1].skills) }}</em>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle', 'titleAliasChunks'],
  computed: {
    skillCounter () {
      return Math.ceil(this.content.data.length / 2)
    }
  },
  methods: {
    skillString (skillObj) {
      let skills = []
      for (const skill of skillObj) {
        skills.push(skill.name)
      }

      return skills.join(', ')
    }
  },
  updated() {
    this.$emit('onUpdate')
  }
}
</script>