<template>

  <select
    :value="value"
    @input="$emit('input', $event.target.value)"
    :class="[
      value == 0 ? 'text-gray-500' : '',
      white ? 'bg-white border-white focus:border-none' : ' border border-gray-400 '
    ]"
    class="form-select w-full py-3 px-3 pr-8 text-gray border border-gray-400 rounded focus:outline-none text-xs font-normal focus:bg-white focus:border-teal-600"
  >
    <option v-if="placeholder" :value="null" disabled class="text-gray-500" selected="selected">
      {{ placeholder }}
    </option>
    <slot></slot>
  </select>

</template>

<script>
export default {
  name: 'Select',
  props: ['placeholder', 'white', 'value']
}
</script>

<style scoped>
select:focus {
  box-shadow: none !important;
}
</style>
