<template>
<div class="slider-template cursor-pointer cursor-pointer transform transition duration-500 hover:scale-105">
  <img v-imgSrc="`template/${cover}`" class="shadow">
  <div class="overlay flex flex-col items-center justify-between p-5 px-8">
    <div></div>
    <div class="flex flex-col justify-center items-center">
      <span class="text-sm text-secondary font-medium">Free</span>
    </div>
    <button @click="doPreview(title)" class="w-20 bg-primary-gradient rounded text-white flex text-base justify-center items-center py-1 hover:bg-secondary">
      Select
    </button>
  </div>
</div>
</template>

<script>
export default {
  props: ['title', 'cover', 'type'],
  methods: {
    doPreview() {
      this.$store.commit('main/setPreviewtemplate', this.title)
      if( this.type == 'resume'){
        if (this.$route.path.includes('resume')) {
          this.$store.dispatch('resumes/update')
        }else {
          this.$router.push('/workspace/resume/content');
        }
      }
      if(this.type == 'coverletter') {
        if (this.$route.path.includes('coverletter')) {
          this.$store.dispatch('coverletter/update')
        }else {
          this.$router.push('/workspace/coverletter/content');
        }
      }
    }
  }
}
</script>