<template>
  <div>
    <div
      @click="toggleCard"
      v-if="data.icon || data.title"
      class="flex md:flex-no-wrap flex-wrap rounded-t justify-between items-center py-5 md:pl-0 md:pr-3 px-4 relative z-10 row-card bg-transparent border-b"
    >
      <div class="flex flex-wrap items-start w-full px-0 md:mb-0 mb-3">
        <div class="md:w-12 w-full flex md:justify-end justify-start md:pr-4 md:pt-2">
          <img v-if="data.icon" :src="data.icon" class="h-8" />
        </div>
        <div class="md:w-10/12 w-full">
          <h1 v-if="data.title" class="text-primary-3 text-lg font-semibold">{{ data.title }}</h1>
          <p class="text-primary-2 text-xs break-all">{{ data.text }}</p>
        </div>
      </div>

      <div class="flex items-center space-x-4">
          <button class="no-filled button-primary rounded-full w-full flex text-xs justify-center items-center px-5 py-1 hover:bg-secondary"
            @click="gotoSection()">
            <img src="/static/img/icon/add-icon.svg" svg-inline class="mr-1"> Add
          </button>
      </div>
    </div>
    <div v-if="open" :class="['flex flex-col space-y-8 bg-white p-6 rounded-b', newClass]">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/main'

export default {
  name: 'AddSectionRow',
  props: {
    newClass: String || Array,
    open: Boolean,
    data: Object,
    index: String
  },
  data() {
    return {
      show: false
    }
  },
  methods: {
    toggleCard() {
      this.show = !this.show
    },
    gotoSection() {
      this.$store.commit('rcontent/addContent', this.data.id)
      let id = this.$store.state.rcontent.content.addedContents[this.data.id].data.length
      this.$store.commit('rcontent/addContentData', this.data.id)
      eventBus.$emit('close-dialog')
      this.$router.push(this.data.redirect + '/' + id)  
    }
  }
}
</script>

<style lang="scss">
$coolTransition: all 0.4s cubic-bezier(1, 0.25, 0, 0.75) 0s;

.row-card {
  transition: $coolTransition;
}
</style>
