const mutations = {
  resetResumeContent (state) {
    state.content = JSON.parse(JSON.stringify(state.defaultcontent));
  },
  setResumeContent (state, data) {
    state.content = data;
  },
  addContent (state, data) {
    Vue.set(state.content.addedContents, data, state.content.contents[data])
    state.content.defaultOpen = data
    Vue.delete(state.content.contents, data)
  },
  removeContent (state, data) {
    state.content.addedContents[data].data = []
    Vue.set(state.content.contents, data, state.content.addedContents[data])
    Vue.delete(state.content.addedContents, data)
  },
  addContentData (state, data) {
    state.content.addedContents[data].data.push(
      cloneobject(state.content.addedContents[data].form)
    )
    state.content.defaultOpen = data
  },
  updateContentData (state, data) {
    state.content.addedContents[data.key].data[data.index] = data.data
    state.content.defaultOpen = data.key
  },
  updateContentOrder (state, data) {
    state.content.addedContents[data.key].data = data.data
  },
  removeContentByIndex (state, data) {
    state.content.addedContents[data.key].data.splice(data.index, 1)
  },
  setDefaultOpen (state, data) {
    state.content.defaultOpen = data
  },
  setPersonal (state, data) {
    state.content.personal = data
  },
  renameSectionAlias (state, data) {
    state.content.addedContents[data.key].titleAlias = data.value
  },
  renameProfileAlias (state, data) {
    state.content.profile.titleAlias = data.value
  },
  addCustomContent (state) {
    let i = 1;
    let name = 'custom1';
    while (name in state.content.addedContents) {
      i++;
      name = 'custom' + i;
    }
    Vue.set(state.content.contents, name, {
      redirect: '/workspace/resume/content/' + name,
      title: 'Custom',
      icon: '/static/img/icons-primary/Setting.svg',
      text: 'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy.',
      id: name,
      data: [],
      titleAlias: 'Custom',
      form: {
        custom: ''
      }
    })
  }
}
  
export default {
  namespaced: true,
  state,
  mutations,
} 