import { auth } from '../../api';
import router from '@/router';

const state = () => ({
  user: {},
  loggedIn: 0,
})

const actions = {
  async signup(store, payload) {
    return await auth.signup(payload)
  },
  async signin(store, payload) {
    return await auth.signin(payload)
  },
  async me({ commit }) {
    const res = await auth.me()
    if (res.status) {
      localStorage.token = ''
      localStorage.selectedresume = ''
      router.push('/')

    } else {
      commit('setUser', res)
      commit('makeLoggedIn')
    }
    return res
  },
}

const mutations = {
  setUser (state, res) {
    state.user = res
  },
  makeLoggedIn (state) {
    state.loggedIn = 1
  },
  makeLoggedOut (state) {
    state.loggedIn = 0
  }
}

export default {
  namespaced: true,
  actions,
  state,
  mutations
}