<template>
  <div
    id="app"
    class="flex -lg:flex-col h-screen bg-primary-2 font-Poppins relative overflow-y-hidden w-full"
  >
    <Sidebar :menuCollapse="menuCollapse" />
    <div
      class="overflow-y-auto w-full flex"
      style="height: 100vh; overflow: auto"
    >
      <div
        :class="[
          $route.name != 'dashboard' &&
          $route.name != 'profile' &&
          $route.name != 'setting' &&
          $route.name != 'resumeTemplates' &&
          $route.name != 'coverletterTemplates' &&
          !$route.params.template
            ? 'view'
            : 'flex flex-1 flex-col'
        ]"
        class="overflow-y-auto h-full"
        id="scrollbar"
        :style="{ width: routeWidth + '%'}"
      >
        <!-- <button
          class="focus:outline-none button-closes w-8"
          @click="menuCollapse = !menuCollapse"
        >
          <img class="w-6 m-2" src="/static/img/icon/heroicons/menu.svg" />
        </button> -->
        <transition>
           <router-view/>
        </transition>
      </div>
      <div
        v-if="isPreview"
        class="h-full relative"
        :class="previeScrollClass"
        :style="{ width: previewWidth + '%' }"
        id="temppreview"
      >
        <Preview />
      </div>
    </div>
    <div class="bg-primary-1 flex justify-center items-center">
      <button
        @click="show"
        class="rounded px-4 py-4 text-white focus:outline-none flex items-center justify-center space-x-2 lg:hidden"
      >
        <img class="w-6" src="/static/img/icon/heroicons/zoomin-white.svg" />
        <span>Preview</span>
      </button>
    </div>
    <modal
      :maxWidth="795"
      height="auto"
      width="100%"
      :adaptive="true"
      name="PreviewModal"
      :styles="{ background: 'transparent', padding: 0 }"
    >
      <Preview :isTemplate="true" />
    </modal>
    <modal
      height="auto"
      width="100%"
      :scrollable="true"
      name="FullPreviewModal"
    >
      <div id="preview-pages"></div>
    </modal>
    <a href="" id="downloadlink" style="display: none"></a>
  </div>
</template>

<script>
import Preview from '@/components/Layouts/Preview.vue'
import Sidebar from '@/components/Layouts/Sidebar.vue'
import ResizeMixin from '@/utils/ResizeHandler'
import { eventBus } from '@/main'

export default {
  components: {
    Preview,
    Sidebar
  },
  data: () => ({
    menuCollapse: false
  }),
  mixins: [ResizeMixin],
  methods: {
    show() {
      this.$modal.show('PreviewModal')
    },
    hide() {
      this.$modal.hide('PreviewModal')
    }
  },
  computed: {
    routeWidth() {
      if (this.isPreview) {
        if (this.$store.state.fullPreview) {
          return 0
        }
        return 55
      }
      return 100
    },
    previewWidth() {
      if (this.$store.state.fullPreview) {
        return 100
      }
      return 45
    },
    isPreview() {
      if (
        !this.$store.getters.getScreen &&
        this.$route.name != 'dashboard' &&
        this.$route.name != 'profile' &&
        this.$route.name != 'setting' &&
        this.$route.name != 'welcome' &&
        this.$route.name != 'resumeTemplates' &&
        this.$route.name != 'coverletterTemplates' &&
        this.$route.name != null
      ) {
        return true
      }
      return false
    },
    previeScrollClass() {
      if (
        this.$store.state.totalPreviewPages == 1 &&
        !this.$store.state.fullPreview
      ) {
        return 'overflow-hidden'
      }
      return 'overflow-y-auto'
    }
  },
  watch: {
    $route() {
      this.$store.commit('fullPreviewMode', false)
    }
  },
  beforeMount() {
    eventBus.$on('initialize', () => {
      this.$store.commit('clcontent/resetCoverletterContent')
      // this.$store.commit('cldesign/resetCoverletterDesign')
      this.$store.commit('rcontent/resetResumeContent')
      // this.$store.commit('rcontent/addContent', 'experience')
      // this.$store.commit('rcontent/addContent', 'education')
      this.$store.commit('cldesign/setDefaultSetting')
    })
    eventBus.$emit('initialize')
    eventBus.$on('authenticated', (route) => {
      if (localStorage.token) {
        this.$store.state.fullScreenProgress = this.$loading.show()
        this.$store.dispatch('user/me')
        .then(() => {
          this.$store.state.fullScreenProgress.hide()
          if (this.$route.path !== route){
            this.$router.push(route)
          }
        })
      }
    })
  },
}
</script>
