import { empty } from '@/utils'

/**
 * Number formatting
 * like 10000 => 10k
 * @param {number} num
 * @param {number} digits
 */
export function numberFormatter(num, digits) {
  const si = [
    { value: 1e18, symbol: 'E' },
    { value: 1e15, symbol: 'P' },
    { value: 1e12, symbol: 'T' },
    { value: 1e9, symbol: 'G' },
    { value: 1e6, symbol: 'M' },
    { value: 1e3, symbol: 'k' }
  ]
  for (let i = 0; i < si.length; i++) {
    if (num >= si[i].value) {
      return (
        (num / si[i].value + 0.1)
          .toFixed(digits)
          .replace(/\.0+$|(\.[0-9]*[1-9])0+$/, '$1') + si[i].symbol
      )
    }
  }
  return num.toString()
}

/**
 * 10000 => "10,000"
 * @param {number} num
 */
export function toThousandFilter(num) {
  return (+num || 0)
    .toString()
    .replace(/^-?\d+/g, m => m.replace(/(?=(?!\b)(\d{3})+$)/g, ','))
}

function toDate(time, format) {
  var normalized = time.replace(/[^a-zA-Z0-9]/g, '-')
  var normalizedFormat = format.toLowerCase().replace(/[^a-zA-Z0-9]/g, '-')
  var formatItems = normalizedFormat.split('-')
  var dateItems = normalized.split('-')

  var monthIndex = formatItems.indexOf('mm')
  var dayIndex = formatItems.indexOf('dd')
  var yearIndex = formatItems.indexOf('yyyy')
  var hourIndex = formatItems.indexOf('hh')
  var minutesIndex = formatItems.indexOf('ii')
  var secondsIndex = formatItems.indexOf('ss')

  var today = new Date()

  var year = yearIndex > -1 ? dateItems[yearIndex] : today.getFullYear()
  var month = monthIndex > -1 ? dateItems[monthIndex] - 1 : today.getMonth() - 1
  var day = dayIndex > -1 ? dateItems[dayIndex] : today.getDate()

  var hour = hourIndex > -1 ? dateItems[hourIndex] : today.getHours()
  var minute = minutesIndex > -1 ? dateItems[minutesIndex] : today.getMinutes()
  var second = secondsIndex > -1 ? dateItems[secondsIndex] : today.getSeconds()

  return new Date(year, month, day, hour, minute, second)
}

/**
 * @param {string} time
 */
export function timeAgo(time) {
  if (empty(time)) {
    return ''
  }

  const between = (Date.now() - toDate(time, 'hh:ii:ss dd/mm/yyyy')) / 1000

  if (between < 3600) {
    return ~~(between / 60) + ' pht trc'
  } else if (between < 86400) {
    return ~~(between / 3600) + ' gi trc'
  } else {
    return ~~(between / 86400) + ' ngy trc'
  }
}

export function validData(data) {
  if (empty(data)) {
    return ''
  }

  return data
}

export function addMoreSemi(data) {
  if (empty(data)) {
    return ''
  }

  return `${data},`
}

export function addMoreDot(data) {
  if (empty(data)) {
    return ''
  }

  return `${data}.`
}
