<template>
  <div class="relative">
    <div class="bg-white p-5 shadow fixed w-full z-10">
      <div class="flex items-center">
        <a @click="goBack" class="cursor-pointer hover:opacity-50">
          <img src="/static/img/icon/left-arrow.svg" class="inline">
        </a> 
        <h1 class="text-xl font-semibold text-primary-3 ml-4">Select Template</h1>
      </div>
      <p class="text-primary-4 text-sm">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
    </div>
    <div class="m-8 mt-32">
      <!-- Template List -->
      <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-3 2xl:grid-cols-3 gap-8 md:pr-24">
        <div class="col-span-1" v-for="item in templateList" :key="item">
          <div class="template-container cursor-pointer cursor-pointer transform transition duration-500 hover:scale-110">
            <img v-imgSrc="`template/${item}.png`" class="shadow radius-10">
            <div class="overlay flex flex-col items-center justify-between p-5">
              <div></div>
              <div class="flex flex-col justify-center items-center">
                <p class="text-sm font-bold text-white">Formal Template</p>
                <span class="text-xs text-secondary font-medium">Free</span>
              </div>
              <button @click="redirect(item)" class="bg-primary-gradient rounded w-full text-white flex text-base justify-center items-center py-1 hover:bg-secondary">
                Select
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="md:pr-16">
        <Pagination v-if="(perPage<total)"
          :current="currentPage"
          :total="total"
          :per-page="perPage"
          @page-changed="fetchTemplates($event)"/>
      </div>
    </div>
  </div>
</template>

<script>
import BackNavigationMixin from "@/mixins/BackNavigationMixin";
import Pagination from '@/components/Ui/Pagination'
export default {
  name: 'TemplatesResume',
  mixins: [BackNavigationMixin],
  components: {
    Pagination
  },
  data() {
    return {
      templates: [],
      templateList: [],
      currentPage: 1,
      perPage: 3,
      total: 0
    }
  },

  mounted() {
    this.templates = [];
    this.templates = this.$store.state.main.resumetemplates
    this.total = this.templates.length;
    this.fetchTemplates(this.currentPage);
  },
  computed: {
  },
  methods: {
    // Pagination
    fetchTemplates(_page) {
      this.currentPage = _page;
      this.templateList = this.templates.slice((_page-1)*this.perPage,(_page-1)*this.perPage+this.perPage)
    },
    redirect(title) {
      this.$store.commit('main/setPreviewtemplate', title)
      this.$router.push('/workspace/resume/content');
    }
  }
}
</script>

<style lang="scss" scoped>
</style> 