<template>
  <div :style="bodyStyle" v-if="content.data.length">
    <div
      v-for="(data, index) in content.data"
      :key="index"
    >
      <p 
        v-if="!index"
        :style="sectionTitleStyle"
      >
        {{ content.titleAlias }}
      </p>
      <div style="display: flex; justify-content: space-between;">
        <div>
          <p style="padding: 6px 0"> 
            <strong style="font-weight: 600; color: #000">
              {{ data.name }}
            </strong>
            {{ data.organization }} 
          </p>
        </div>
        <div style="font-size: 14px; padding: 6px 0">
          <p> 
            {{ data.email ? 'E-mail:' : '' }} {{ data.email }} 
          </p>
          <p> 
            {{ data.phone ? 'Phone:' : '' }} {{ data.phone }} 
          </p>
        </div>  
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['content', 'bodyStyle', 'sectionTitleStyle'],
  updated() {
    this.$emit('onUpdate')
  }
}
</script>