  <div class="m-8">
    <h1 class="text-2xl font-bold text-primary-3">Explore Now</h1>
    <p class="text-primary-4">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
    <!-- Cards (Service List) -->
    <div class="grid grid-cols-1 md:grid-cols-3 xl:grid-cols-4 gap-8 mt-8">
      <div class="col-span-1 cursor-pointer" v-for="item in serviceList" :key="item.icon">
        <div
          class="w-full radius-10 h-full transform transition duration-500 hover:scale-110"
          :style="item.style"
          @click="openService(item.name)"
        >
          <div class="w-full flex flex-row justify-center h-full p-6 px-12 min-h-250">
            <div class="flex flex-col items-center justify-between">
                <img :src="'/static/img/home/'+item.icon" svg-classes=""/>
                <p class="text-lg font-bold">{{item.title}}</p>
                <p class="text-dark text-sm font-normal text-center mt-2">{{item.description}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Template List -->
    <div class="grid grid-cols-1 lg:grid-cols-2 gap-8 mt-8">
      <div class="col-span-1 radius-10 card-shadows p-8 bg-white">
        <div class="flex justify-between">
          <h1 class="text-2xl-0 font-semibold">Resume Templates</h1>
          <router-link :to="{ name: 'resumeTemplates'}" class="text-lg text-primary font-medium cursor-pointer">
            See all
          </router-link>
        </div>
        <div class="grid grid-cols-12 mt-5">
          <div class="col-span-12"> 
            <TemplateContainer :type="'resume'" :customSettings="settings"/>
          </div>
        </div>
      </div>
      <div class="col-span-1 radius-10 card-shadows p-8 bg-white">
        <div class="flex justify-between">
          <h1 class="text-2xl-0 font-semibold">Cover Letter Templates</h1>
            <router-link :to="{ name: 'coverletterTemplates'}" class="text-lg text-primary font-medium cursor-pointer">
              See all
            </router-link>
        </div>
        <div class="grid grid-cols-12 mt-5">
          <div class="col-span-12"> 
            <TemplateContainer :type="'coverletter'" :customSettings="settings"/>
          </div>
        </div>
      </div>
    </div>
    <!-- Explore -->
    <div class="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8 setting-options mt-8">
      <div class="col-span-1 cursor-pointer bg-white radius-10 card-shadows" v-for="item in exploreList" :key="item.icon">
        <div class="w-full h-full">
          <div class="w-full flex flex-row justify-between h-full">
            <div class="grid grid-cols-3">
              <div class="col-span-1">
                <div class="flex justify-center items-center rounded-lg bg-primary-light m-4 w-28 h-28">
                  <img :src="'/static/img/icon/'+item.icon" svg-classes=""/>
                </div>
              </div>
              <div class="col-span-2 m-4 my-6">
                <div class="grid grid-cols-1 h-full">
                  <div class="col-span-1">
                    <p class="text-lg font-semibold">{{item.title}}</p>
                  </div>
                  <div class="col-span-1 flex items-end">
                    <button class="no-filled button-primary rounded-full text-white flex text-base justify-center items-center px-5 transform transition duration-500 hover:scale-90 hover:bg-gray-500">
                      Explore  <iconly name="arrow-right" type="light" />
                    </button>    
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Card from '@/components/Ui/Card'
import { eventBus } from '@/main'
import SlickCarouselMixin from '@/mixins/SlickCarouselMixin'
import TemplateContainer from '@/components/Design/TemplateContainer'
export default {
  name: 'Home',
  components: {
    // Card
    TemplateContainer
  },
  mixins: [SlickCarouselMixin],
  data() {
    return {
      name: '',
      cards: [
        {
          img: '/static/img/home/resumecv.png',
          title: 'Resume',
          text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit',
          checked: false
        },
        {
          img: '/static/img/home/coverLetter.png',
          title: 'Coverletter',
          text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit',
          checked: false
        },
        {
          img: '/static/img/home/website.png',
          title: 'Wesbite',
          text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit',
          checked: false
        },
        {
          img: '/static/img/home/hire.png',
          title: 'Hire a professional',
          text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit',
          checked: false
        }
      ],
      serviceList: [
        {
          name: 'Resume',
          icon: 'resume.svg',
          style: { backgroundColor: '#F0EDFF' },
          title: 'Resume',
          description: 'Lorem ipsum dolor sit amet, consectetur'
        },
        {
          name: 'Coverletter',
          icon: 'cover-letter.svg',
          style: { backgroundColor: '#FFECDC' },
          title: 'Cover Letter',
          description: 'Lorem ipsum dolor sit amet, consectetur'
        },
        {
          name: 'Website',
          icon: 'website.svg',
          style: { backgroundColor: '#DEF2FF' },
          title: 'Wesbite',
          description: 'Lorem ipsum dolor sit amet, consectetur'
        },
        {
          name: 'Hire',
          icon: 'hire.svg',
          style: { backgroundColor: '#E6FAD6' },
          title: 'Hire a Professional',
          description: 'Lorem ipsum dolor sit amet, consectetur'
        }
      ],
      exploreList: [
        {
          title: 'Learn how to write a CV',
          icon: 'copywriting.svg',
          url: ''
        },
        {
          title: 'Awesome Cover Letter Tricks',
          icon: 'reward.svg',
          url: ''
        },
        {
          title: 'Why should you hire a proffessional?',
          icon: 'tip-document-editing.svg',
          url: ''
        }
      ],
      selectedCard: ''
    }
  },

  mounted() {
    eventBus.$on('add-selected-card', index => {
      if (this.cards[index].checked) {
        this.cards[index].checked = false
        return
      }

      this.cards.forEach(card => (card.checked = false))
      this.cards[index].checked = true
      this.selectedCard = this.cards[index].title
    });
    
  },
  methods: {
    continueRedirect: function () {

      this.$store.commit('main/updateDashboardName', this.name);

      if (this.selectedCard) {
        switch (this.selectedCard) {
          case "Coverletter":
            this.$store.commit('clcontent/resetCoverletterContent');
            this.$router.push('/workspace/coverletter/content');
            break;

          case "Resume":
            this.$store.commit('rcontent/resetResumeContent');
            this.$store.commit('rcontent/addContent', 'experience')
            this.$store.commit('rcontent/addContent', 'education')
            localStorage.selectedresume= ''
            this.$router.push('/workspace/resume/content');
            break;
        }
      }
      
    },
    openService: function (service_name) {
      if (service_name) {
        switch (service_name) {
          case "Coverletter":
            this.$store.commit('clcontent/resetCoverletterContent');
            this.$router.push('/workspace/coverletter/content');
            break;

          case "Resume":
            this.$store.commit('rcontent/resetResumeContent');
            localStorage.selectedresume= ''
            this.$router.push('/workspace/resume/content');
            break;
        }
      }
      
    }
  }
}
</script>

