<template>
  <div class="flex">
    <img 
     v-for="n in level" 
     src="/static/img/icon/heroicons/circle-filled.svg" 
     :key="n"
     class="w-4 h-4 mr-1"
    >
    <img 
     v-for="n in (5 - level)" 
     src="/static/img/icon/heroicons/circle.svg" 
     :key="n + level"
     class="w-4 h-4 mr-1"
    >
    <span  class="absolute inset-y-0 right-0 flex items-center pr-4">
      <img 
        v-show="isSelected"
        class="h-5 w-5"
        src="/static/img/icon/heroicons/check.svg"
      >
    </span>
  </div>
</template>
<script>
export default {
  props: {
    level: Number,
    value: Number,
  },
  data () {
    return  {
    }
  },
  computed: {
    isSelected () {
      return this.level === this.value
    }
  }
}
</script>