<template>
  <div class="bg-white rounded">
    <div class="flex justify-between p-4">
      <div class="flex space-x-4">
        <img :src="icon" class="w-6" />
        <h1 class="text-primary-1 text-xl font-bold">{{ title }}</h1>
      </div>
      <div @click="openComponant()" class="cursor-pointer">
         <img 
          src="/static/img/icon/heroicons/chevron-down.svg" 
          class="mt-2 ml-3 transform w-5" 
          :class="{ 'rotate-180': openComponantFlag }"
         />
      </div>
    </div>
    <hr />
    <slot></slot>
  </div>
</template>
<script>
export default {
  props: ['title', 'icon','isVisible'],
  data() {
    return {
      openComponantFlag: this.isVisible
    }
  },
  methods: {
    openComponant() {
      if (this.openComponantFlag) {
        this.openComponantFlag = false
      } else {
        this.openComponantFlag = true
      }
      this.$emit('collapseEvent', this.openComponantFlag, this.title)
    }
  },
  computed: {}
}
</script>