import { pdf } from '../../api';

const state = () => ({
  previewtemplate: null,
  coverletters: [
    'contentLetter1',
    'contentLetter2',
    'contentLetter3',
    'contentLetter4',
    'contentLetter5',
    'contentLetter6',
  ],
  resumetemplates: [
    'resume1',
    'resume2',
    'resume3',
  ],
  dashboardName: '',
})

const mutations = {
  setPreviewtemplate (state, template) {
    state.previewtemplate = template
  },
  updateDashboardName(state, data) {
    state.dashboardName = data
  },
}

const actions = {
  async downloadpdf(store, payload) {
    let response = await pdf.htmltopdf(payload)
    const blob = new Blob([response], {type: 'application/pdf'})
    const link = document.getElementById('downloadlink')
    link.href = window.URL.createObjectURL(blob)
    link.download = payload.filename + '.pdf'
    link.click()
    window.URL.revokeObjectURL(link.href)
    return
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
}