<template>
  <div class="grid grid-cols-12">
    <div class="col-span-12"> 
      <SlickCarousel v-bind="settings" ref="resumeCarousel">
        <CardTemplate
          v-for="template in templateList"
          :key="template"
          :cover="`${template}.png`"
          :title="template"
          :type="type"
        />
        <template #prevArrow="arrowOption">
          <div class="custom-arrow">
              <iconly name="arrow-leftsquare" type="bold" class="h-8 w-8 text-primary"/>
            {{ arrowOption.currentSlide }}/{{ arrowOption.slideCount }}
          </div>
        </template>
        <template #nextArrow="arrowOption">
          <div class="custom-arrow">
              <iconly name="arrow-rightsquare" type="bold" class="h-8 w-8 text-primary"/>
            {{ arrowOption.currentSlide }}/{{ arrowOption.slideCount }}
          </div>
        </template>
      </SlickCarousel>
    </div>
  </div>
</template> 


<script>
import CardTemplate from '../Design/CardTemplate'
export default {
  components: {
    CardTemplate
  },
  data() {
    return {
      active: 'resume',
      settings: {
        "infinite": false,
        "speed": 2000,
        "slidesToShow": 4,
        "slidesToScroll": 4,
        "initialSlide": 0,
        "responsive": [
        {
            "breakpoint": 4000,
            "settings": {
                "slidesToShow": 8,
                "slidesToScroll": 8,
                "infinite": true
            }
        },
        {
            "breakpoint": 3000,
            "settings": {
                "slidesToShow": 6,
                "slidesToScroll": 6,
                "infinite": true
            }
        },
        {
            "breakpoint": 2000,
            "settings": {
                "slidesToShow": 5,
                "slidesToScroll": 5,
                "infinite": true
            }
        },
            {
            "breakpoint": 1850,
            "settings": {
                "slidesToShow": 4,
                "slidesToScroll": 4
            }
        },
        {
            "breakpoint": 600,
            "settings": {
                "slidesToShow": 2,
                "slidesToScroll": 2,
                "initialSlide": 2
            }
        },
            {
            "breakpoint": 480,
            "settings": {
                "slidesToShow": 1,
                "slidesToScroll": 1
            }
        }
        ]
      }
    }
  },
  props: {
    'type': {
      type: String,
      required: true
    },
    'customSettings': {
      type: Object,
      required: false,
      default() {
        return {};
      }
    }
  },
  computed: {
    templateList() {
      let templates = [];
      if (this.type == 'coverletter') {
        templates = this.$store.state.main.coverletters
      }
      if (this.type == 'resume') {
        templates = this.$store.state.main.resumetemplates
      }
      return templates
    }
  },
  watch : {
    'customSettings': {
      handler: function(s) {
        if(s['dots']) {
          this.settings = this.customSettings;
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    handleChange: function(e) {
      this.active = e
    }
  }
}
</script>

<style scoped>
.mbb {
  margin-bottom: -2px;
}
</style>
