<template>
  <div>
    <div v-if="icon || title" 
        class="bg-white rounded card-shadows px-5 on-hover-shadow-light" :class="[$route.fullPath.includes('coverletter') ? 'cursor-pointer' : '']">
      <div class="flex space-x-4 border-b py-5 items-center px-0" @click="toggleCard">
        <img v-if="icon" :src="icon" class="h-8">
        <div class="flex-col">
          <h1 v-if="title" class="text-primary-1 text-lg font-semibold">{{ title }}</h1>
          <p v-if="description" class="text-primary-2 text-xs">{{description}}</p>
        </div>
      </div>
      <div v-if="open" :class="['flex flex-col space-y-5 py-5 pb-10', newClass]">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'CardEditContent',
  props: {
    title: String,
    icon: String,
    description: String,
    newClass: String || Array,
    checkOpen: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      open: false
    }
  },
  watch: {
    checkOpen(val) {
      this.open = val
    }
  },
  mounted() {
    this.open = this.checkOpen
  },
  methods: {
    toggleCard() {
      if (this.$route.fullPath.includes('coverletter')) {
        this.open = !this.open
      } else {
        return
      }
    },
  }
}
</script>
